import './WorkflowDetails.scss';
import React, { useCallback } from "react";
import { useWorkflows } from "../hooks/useWorkflows";
import { Label } from "../core/Label";
import { Property } from "./PropertyEditor/Property";
import { useDispatchQuestionnaire } from "../hooks/useDispatch";
import { PropertyKeyValue } from "../../questionnaires/entities/anyObject";

export const WorkflowDetails = () => {
  const {currentWorkflow} = useWorkflows()
  const {changeItemPropertyValue, changeKeyPrefixes} = useDispatchQuestionnaire()
  const {info: {name, keyPrefix, __uuid}, questions} = currentWorkflow || {}

  const setPrefix = useCallback((p: PropertyKeyValue) => {
    console.log('setPrefix', keyPrefix, p.key, p.value, __uuid)
    changeItemPropertyValue({...p, __uuid})
    changeKeyPrefixes({
      before: keyPrefix || '',
      after: p.value as string,
      qs: questions
    })
  }, [questions, keyPrefix, __uuid, changeItemPropertyValue, changeKeyPrefixes]);

  return (
    <div className={`WorkflowDetails`}>
      <Label text={`Workflow details`}/>
      <Label className="title" text={name}/>
      {name !== 'all workflows' ?
        <Property label="Key prefix" name="keyPrefix" value={keyPrefix} type={`string`} onChange={setPrefix}
                  changeOnBlurOnly/> : null}
      <div>_</div>
      <Property name="Number of questions" value={questions.length} type="number" disabled/>
    </div>
  )
}
