import React, { FC, PropsWithChildren } from 'react';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { If } from "./If";
import { IconProp } from "@fortawesome/fontawesome-svg-core";
import { Label } from "./Label";
import './Btn.scss';
import { Map } from "./Map";
import * as _ from "lodash";

export interface BtnProps extends React.DetailedHTMLProps<React.ButtonHTMLAttributes<HTMLButtonElement>, HTMLButtonElement> {
  icon?: IconProp | IconProp[],
  label?: string,
  highlight?: boolean,
  highlightColor?: string,
  toolTip?: string,
}

export const Btn: FC<PropsWithChildren<BtnProps>> = (
  {
    icon,
    label,
    highlight,
    highlightColor,
    toolTip,
    onClick,
    className,
    style,
    ...props
  }) => {
  const hStyle = {"--highlight-color": highlightColor} as React.CSSProperties;
  const icons = _.castArray<IconProp>(icon);

  return <button onClick={onClick} className={`Btn ${className} ${highlight ? 'highlight' : ''}`}
                 style={{...style, ...hStyle}} {...props} >
    <Label text={label}/>
    {props.children}
    <If condition={icon}>
      <div className="Icon">
        <Map
          items={icons}
          itemRenderer={(ic, i) => <FontAwesomeIcon icon={ic} key={i}/>}
        />
      </div>
    </If>
    <Label text={toolTip} className='Tooltip'/>
  </button>;
};
