import { QuestionUnion } from "../../questionnaires/entities/question";
import { useCallback, useMemo } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCommentAlt, faKeyboard, faTrashAlt } from "@fortawesome/free-regular-svg-icons";
import { faBarcode, faCamera, faGreaterThan, faPlus } from "@fortawesome/free-solid-svg-icons";
import { useDispatchQuestionnaire } from "../hooks/useDispatch";
import { PropertyPair } from "../Details/PropertyEditor/PropertyPair";
import { Btn } from "../core/Btn";
import { createUniqueObject } from "../../questionnaires/entities/anyObject";
import { useHotkeys } from "react-hotkeys-hook";
import { createTrigger } from "../../questionnaires/entities/trigger";

type QuestionDetailsProps = {
  q: QuestionUnion
}

function MessageDetails({q}: QuestionDetailsProps) {
  return <div>
    <FontAwesomeIcon icon={faCommentAlt} style={{margin: '0 10', fontSize: '1.5em'}}/>
    <span>{q.Message || q.MessageSelector?.join('.') || '...'}</span>
  </div>
}

function TextDetails({q}: QuestionDetailsProps) {
  return <div>
    <FontAwesomeIcon icon={q.InputType === 'barcode' ? faBarcode : faKeyboard}/>
    {q.InputType === 'number' ? '123' : q.InputType === 'text' ? 'abc' : ''}
  </div>
}

function OptionsDetails({q}: QuestionDetailsProps) {
  const {changeItemPropertyValue, updateQuestion} = useDispatchQuestionnaire();

  const onPropertyChange = useCallback((uuid: string, key: string) => (value: string) => {
    changeItemPropertyValue({__uuid: uuid, key, value});
  }, [changeItemPropertyValue]);

  const onRemoveOption = useCallback((uuid: string) => () => {
    if (!q.Options) return
    const deadO = q.Options.find(o => o.__uuid === uuid)!;
    const Triggers = q.Triggers?.filter(tr => tr.TriggerType !== "Equals" || tr.Value !== deadO.Value)
    const Options = q.Options.filter(o => o.__uuid !== uuid);
    updateQuestion({...q, Options, Triggers});
  }, [updateQuestion, q]);

  const onAddOption = useCallback(() => {
    if (!q.Options) return
    const Value = q.Options.reduce((v, o) => Math.max(v, o.Value), 0) + 1;
    const Options = [...q.Options, createUniqueObject({Title: '', Value})];
    const Triggers = q.Triggers && q.Triggers.concat({...createTrigger("Equals"), Value})
    updateQuestion({...q, Options, Triggers});
  }, [updateQuestion, q]);

  const ref = useHotkeys('ctrl+=', onAddOption)

  if (q.Options && q.Options.length)
    return <div ref={() => ref}>
      <div>Select options</div>
      <ul>
        {q.Options?.map(o => <li key={o.__uuid}>
          <PropertyPair
            valueOne={o.Title}
            valueTwo={o.Value}
            onChangeOne={onPropertyChange(o.__uuid, 'Title')}
            onChangeTwo={onPropertyChange(o.__uuid, 'Value')}
          />
          <Btn icon={faTrashAlt} onClick={onRemoveOption(o.__uuid)}/>
        </li>)}
        <li>
          <span style={{flex: 1}}/><Btn icon={faPlus} onClick={onAddOption}/>
        </li>
      </ul>
    </div>

  if (!q.OptionsSelector) return <div>??????</div>
  const {Selector, Mapping} = q.OptionsSelector![0];
  const option = Selector[0] || '?'
  const txt = option === 'answer' ? `-> ${Selector[1] || '?'}` :
    option.includes('current') ? `${option[1]}` :
      'advanced lookup'
  const extra = Selector.filter(v => v !== 'Items').slice(-1)
  return <div>
    <ul>
      <li>
        <div><span>{option}</span><span>{txt}</span></div>
      </li>
      <li>
        <div><span>{extra}</span><span>{Mapping.Title},{Mapping.Value}</span></div>
      </li>
    </ul>
  </div>
}

function ComparisonsDetails() {
  return <div>
    <span style={{fontSize: '1.5em', fontWeight: 'bold', marginRight: '10'}}>A</span>
    <FontAwesomeIcon icon={faGreaterThan} style={{margin: '0 10', fontSize: '1.5em'}}/>
    <span style={{fontSize: '1.5em', fontWeight: 'bold', marginRight: '10'}}>B</span>
  </div>
}

function HashtagDetails() {
  return <div style={{fontSize: '1.5em', fontWeight: 'bold'}}>#</div>
}

function FunctionDetails() {
  return <div style={{fontSize: '1.5em', fontWeight: 'bold'}}>fn(x)</div>
}

function RequestDetails({q}: QuestionDetailsProps) {
  return <div style={{fontSize: '1em', fontWeight: 'bold'}}>http:// {
    q.RequestType
  }</div>
}

function DimensionsDetails() {
  return <div style={{fontSize: '1.5em', fontWeight: 'bold'}}>
    L x H x B
  </div>
}

function PictureDetails() {
  return <div>
    <FontAwesomeIcon icon={faCamera} style={{margin: '0 10', fontSize: '1.5em'}}/>
  </div>
}

export function useQuestionSpecificComponent(q: QuestionUnion) {
  return useMemo(() => <div className={`Specific ${q.QuestionType}`}>
    {
      q.QuestionType === "Alert" ? <MessageDetails q={q}/> :
        q.QuestionType === "Select" ? <OptionsDetails q={q}/> :
          q.QuestionType === "Text" ? <TextDetails q={q}/> :
            q.QuestionType === "AutoAnswer" ? <ComparisonsDetails/> :
              q.QuestionType === "FunctionCall" ? <FunctionDetails/> :
                q.QuestionType === "RoadlinkRequest" ? <RequestDetails q={q}/> :
                  q.QuestionType === "SingleDigit" ? <HashtagDetails/> :
                    q.QuestionType === "ThreeDimensions" ? <DimensionsDetails/> :
                      q.QuestionType === "Picture" ? <PictureDetails/> :
                        null
    }
  </div>, [q]);
}