import './Property.scss';
import { useMemo } from "react";
import { QuestionKey } from "../../../questionnaires/entities/question";
import { PropertyKeyValue } from "../../../questionnaires/entities/anyObject";
import { usePropState } from "../../hooks/usePropState";
import { useSelector } from "react-redux";
import { RootState } from "../../../models";
import { store } from "../../../store/store";

type KeyEditProps = {
  name: QuestionKey
  label?: string | JSX.Element
  value: any
  onBlur?: (p: PropertyKeyValue) => void
  onFocus?: (event?: { currentTarget: EventTarget & HTMLElement }) => void
}

const questionKeysSelector = store.select.questionnaireModel.questionKeys;
export const KeyEditComponent: React.FunctionComponent<KeyEditProps> = (
  {
    name,
    label,
    value,
    onFocus,
    onBlur,
  }) => {
  const [edit, setEdit] = usePropState<string>(value);
  const questionKeys = useSelector((state: RootState) => questionKeysSelector(state))

  const isValid = useMemo(() => {
    return edit === value || !questionKeys.includes(edit)
  }, [edit, value, questionKeys]);

  const onInnerBlur = () => {
    if (!isValid || value === edit) {
      setEdit(value)
      return;
    }
    onBlur && onBlur({key: name, value: edit});
  }

  return (
    <div
      onFocus={onFocus}
      onClick={onFocus}
      className={`Property KeyEdit`}
    >
      {label !== '' ? <span>{label || name}</span> : null}
      <input
        style={{color: isValid ? 'white' : 'orangered'}}
        value={edit}
        inputMode={'text'}
        onChange={event => {
          setEdit(event.target.value);
        }}
        onBlur={onInnerBlur}
      />
    </div>
  );
}
