import { useDispatchDiagram, useDispatchQuestionnaire } from "./useDispatch";
import { useDiagram, useQuestionnaire } from "./useSelector";
import { useCallback, useMemo } from "react";
import { MenuItem } from "../core/PopupMenu";
import { faNetworkWired, faPaste, faRedo, faShare, faUndo } from "@fortawesome/free-solid-svg-icons";
import { faClone, faCopy } from "@fortawesome/free-regular-svg-icons";
import { useLayoutElements } from "./useLayoutElements";
import { useOsHotkeyCombos } from "./useOsSpecialKeys";
import { EventPosition } from "../../models/diagramModel";

export function useEditingItems() {
  const {undo, redo} = useDispatchQuestionnaire();
  const {past, future} = useQuestionnaire();
  const {copyNodes, pasteNodes, moveNodes, duplicateNodes} = useDispatchDiagram();
  const {questionNodes, edges, selectedNodes, selectedEdges, copiedNodes} = useDiagram();
  const {layoutElements} = useLayoutElements();
  const {cmd, shift} = useOsHotkeyCombos();

  const layoutPart = useCallback(() => {
    if (selectedEdges.length > 0 && selectedNodes.length > 1)
      layoutElements(selectedNodes, selectedEdges);
    else {
      layoutElements(questionNodes, edges)
    }
  }, [selectedNodes, selectedEdges, questionNodes, edges, layoutElements]);

  const pasteMenuItems = useMemo<MenuItem[]>(() => [{
    title: 'Paste',
    icons: [faPaste],
    value: (pos?: EventPosition) => {pasteNodes(pos)},
    disabled: !copiedNodes?.length,
    hotkeys: `${cmd}+v`,
    toolTip: 'Paste copied elements'
  },
    {
      title: 'Move',
      icons: [faShare],
      value: (pos?: EventPosition) => {moveNodes(pos)},
      disabled: !copiedNodes?.length,
      hotkeys: `${cmd}+x`,
      toolTip: 'Move elements over'
    },
    {
      title: 'Duplicate',
      icons: [faClone],
      value: () => {duplicateNodes()},
      disabled: !selectedNodes.length,
      hotkeys: `${cmd}+d`,
      toolTip: 'Duplicate selected elements'
    }], [copiedNodes, selectedNodes, pasteNodes, duplicateNodes, cmd, moveNodes]);

  const copyMenuItems = useMemo<MenuItem[]>(() => [
    {
      title: 'Copy',
      icons: [faCopy],
      value: () => {copyNodes()},
      disabled: !selectedNodes.length,
      hotkeys: `${cmd}+c`,
      toolTip: 'Copy elements'
    },
  ], [copyNodes, selectedNodes.length, cmd]);

  const fullEditMenuItems = useMemo<MenuItem[]>(() => [
    {
      title: 'Undo', icons: [faUndo], value: () => {undo()}, disabled: !past.length,
      hotkeys: `${cmd}+z`,
      toolTip: 'Undo previous action'
    },
    {
      title: 'Redo',
      icons: [faRedo],
      value: () => {redo()},
      disabled: !future.length,
      hotkeys: `${cmd}+${shift}+z`,
      toolTip: 'Redo previous undone action'
    },
    {value: 'separator', title: 'sep1'},
    ...copyMenuItems,
    ...pasteMenuItems,
    {value: 'separator', title: 'sep2'},
    {
      title: 'Layout elements', icons: [faNetworkWired],
      value: layoutPart,
      toolTip: 'Layout all elements of the questionnaire'
    },
  ], [undo, redo, pasteMenuItems, past.length, future, layoutPart, cmd, shift, copyMenuItems]);

  return {
    fullEditMenuItems,
    copyMenuItems,
    pasteMenuItems,
  }
}