import React, { useMemo } from "react";
import { useWorkflows } from "../hooks/useWorkflows";
import './Workflows.scss';
import { If } from "../core/If";
import { Map } from "../core/Map";
import { Column, Row, Static } from "../core/Layout";
import { Btn } from "../core/Btn";
import { Diagram } from "../Diagram/Diagram";
import { Node } from "react-flow-renderer";
import { QuestionUnion } from "../../questionnaires/entities/question";
import { ContentHook } from "../../questionnaires/entities/triggeredBy";

export type WorkflowInfo = {
  __uuid: string,
  name: string,
  keyPrefix?: string,
  contentHook?: ContentHook,
}

export type Workflow = {
  info: WorkflowInfo,
  nodes: Node[],
  questions: QuestionUnion[]
}

export const Workflows = () => {
  const {workflows, currentWorkflow, setWorkflow} = useWorkflows();
  //
  // useEffect(() => {
  //   setWorkflow(workflows[0])
  // }, []);

  const allHint = useMemo(() => "Filter question path by starting trigger"
    , []);

  return (
    <Column className="Workflows">
      <Row {...Static} className="Tabs">
        <div className="Filler"/>
        <Map className="Tabs" items={workflows} itemRenderer={(w, index) => (
          <Btn
            className={`Tab ${w.info.__uuid === currentWorkflow.info.__uuid ? 'selected' : ''}`}
            label={`${w.info.name}`}
            key={index}
            toolTip={allHint}
            onClick={() => setWorkflow(w.info.__uuid)}
          >
          </Btn>)
        }/>
        {/*<Btn*/}
        {/*  className={`Tab`}*/}
        {/*  icon={faPlus}*/}
        {/*  label="Add workflow"*/}
        {/*  toolTip="Add a questionnaire workflow with a starting point"*/}
        {/*  key="_plus_"*/}
        {/*  onClick={() => console.log('Add workflow...')}*/}
        {/*/>*/}
      </Row>
      <If condition={currentWorkflow}>
        <Diagram value={currentWorkflow}/>
      </If>
    </Column>
  )
}
