import { useCallback, useMemo } from "react";
import { QuestionTemplateFactory } from "../../questionnaires/factories/questionFactory";
import { useDispatch } from "react-redux";
import { Dispatch } from "../../models";
import { Position, QuestionUnion } from "../../questionnaires/entities/question";
import { useReactFlow } from "react-flow-renderer";
import { addPositions } from "../../core/addPositions";

export const useAddQuestion = () => {
  const {project} = useReactFlow();
  const {addQuestion} = useDispatch<Dispatch>().questionnaireModel;
  const defaultSpanPosition = useMemo<Position>(() => ({
    x: window.visualViewport.width / 3,
    y: window.visualViewport.height / 4,
  }), []);
  return useCallback((f: QuestionTemplateFactory,
      position?: Position,
      currentWorkflowId?: string,
      callback?: (q: QuestionUnion) => void
    ) => {
      const q = f();
      const __position = addPositions(project(position || defaultSpanPosition), {x: -100, y: -50})
      addQuestion({
        question: {
          ...q,
          __position,
          __workflowId: currentWorkflowId,
        },
        callback
      })
    }
    , [project, addQuestion, defaultSpanPosition]);
}
