import dagre from "dagre";
import { useCallback } from "react";
import { QuestionUnion } from "../../questionnaires/entities/question";
import { useDispatchQuestionnaire } from "./useDispatch";
import { useQuestionnaire } from "./useSelector";
import { Edge, Node } from "react-flow-renderer";

const dagreGraph = new dagre.graphlib.Graph();
dagreGraph.setDefaultEdgeLabel(() => ({}));

export function useLayoutElements() {
  const {updateQuestion} = useDispatchQuestionnaire()
  const {questions} = useQuestionnaire();
  // const {selectedNodes, selectedEdges} = useSelectedNodes(selection);

  const layoutElements = useCallback((selectedNodes: Node<QuestionUnion>[], selectedEdges: Edge<any>[]) => {
    const direction = 'TB';
    dagreGraph.setGraph({
      rankdir: direction,
      marginy: 100,
    });

    const size = (n: any) => ({
      width: n.width + 50,
      height: n.height + 100
    })
    selectedNodes.forEach(n => dagreGraph.setNode(n.id, size(n)))
    selectedEdges.forEach(e => dagreGraph.setEdge(e.source, e.target))

    dagre.layout(dagreGraph);
    updateQuestion(questions.map<QuestionUnion>(q => {
        const node = dagreGraph.node(q.Key);
        if (!node) return q;
        const {x, y, width, height} = node;
        return ({
          ...q,
          __position: {
            x: x - width / 2 + Math.random() / 1000,
            y: y - height / 2,
          }
        });
      }),
    )

  }, [updateQuestion, questions]);

  return {layoutElements}
}