import React, { PropsWithChildren, useCallback} from "react";
import "./ValueComparisonComponent.scss";
import { PropertyKeyValue } from "../../questionnaires/entities/anyObject";
import GivePropertyComponent from "./PropertyEditor";
import { PropertySelectList } from "./PropertyEditor/PropertySelectList";
import { createSelector } from "../../questionnaires/entities/selector";
import { ComparisonValue, createValueComparison } from "../../questionnaires/entities/comparison";
import { useDispatch } from "react-redux";
import { Dispatch } from "../../models";
import { useBuilder } from "../hooks/useBuilder";
import { usePropertyList } from "../hooks/usePropertyList";

export type ComparisonComponentProps = {
  valueComparison: ComparisonValue,
  propName?: string,
}

const Hand = ["Selector", "Value"];

export function ValueComparisonComponent(
  {
    valueComparison,
    propName = '',
  }: PropsWithChildren<ComparisonComponentProps>
) {
  const {changeItemPropertyValue} = useDispatch<Dispatch>().questionEditorModel;
  const builder = useBuilder();
  const propertyList = usePropertyList(valueComparison, propName);

  const onChange = useCallback((prop: PropertyKeyValue) => {
    changeItemPropertyValue({
      ...prop,
      __uuid: valueComparison.__uuid,
    });
  }, [valueComparison.__uuid, changeItemPropertyValue]);

  const propNameChange = useCallback(({key, value}: PropertyKeyValue) => {
      const k = key + value;
      if (propertyList.some(p => p.key === k)) return
      changeItemPropertyValue({
        __uuid: valueComparison.__uuid,
        key: k,
        value: k.endsWith('Value')
          ? [1]
          : k.endsWith('Selector')
            ? createSelector()
            : [createValueComparison()]
      })
    }, [propertyList, valueComparison.__uuid, changeItemPropertyValue]);

  const label = useCallback((key: string) => {
    const hand = key.startsWith('Left') ? 'LeftHand' : 'RightHand';
    return (
      <PropertySelectList
        options={Hand}
        onChange={(p)=> propNameChange(p)}
        value={key.split('Hand')[1]}
        name={hand}
      />
    );
  }, [propNameChange]);

  return (
    <div className={`ValueComparisonComponent`}>
      {propertyList.map(({key, value}) => {
        return <GivePropertyComponent
          key={key.substr(0, 8)}
          name={key}
          label={key.includes('Hand') ? label(key) as JSX.Element : undefined}
          value={value}
          onChange={onChange}
          options={builder.getPropertyValueOptions(key === 'Comparator' ? 'ValueComparator' : key)}
        />
      })}
    </div>
  );
}
