import * as _ from "lodash";
import { useMemo } from "react";
import { MenuItem } from "../core/PopupMenu";
import { QuestionBaseTemplates, QuestionTemplates } from "../../questionnaires/factories/questionFactory";

export function useAddQuestionItems() {
  return useMemo(() => [
      ...Object.entries(QuestionTemplates).map(([k, fac]) => ({
        title: _.startCase(k),
        value: fac,
      } as MenuItem)),
      {value: 'separator'},
      ...Object.entries(QuestionBaseTemplates).map(([k, fac]) => ({
        title: _.startCase(k),
        value: fac,
      } as MenuItem)),
    ]
    , []);
}