import React, { useMemo } from "react";
import { MenuItem } from "../core/PopupMenu";
import { DropDownMenu } from "../core/DropDownMenu";
import { useQuestionEditor } from "../hooks/useSelector";
import { useBuilder } from "../hooks/useBuilder";
import { faBars, faCheck } from "@fortawesome/free-solid-svg-icons";
import { QuestionUnion } from "../../questionnaires/entities/question";
import { useDispatch } from "react-redux";
import { Dispatch } from "../../models";
import { IconDefinition } from "@fortawesome/free-brands-svg-icons";

const questionHasProperty = (q: QuestionUnion, p: string) => Object.keys(q).includes(p)

type Props = {
  label?: string,
  icon?: IconDefinition
}

export function PropertyEditMenu({label, icon = faBars}: Props) {
  const builder = useBuilder()
  const {after} = useQuestionEditor();
  const {addProperty, deleteProperty} = useDispatch<Dispatch>().questionEditorModel;

  const menuItems = useMemo(() => {
    if (!after) return [];
    const createOnItemClick = (propName: string) => () => {
      if (questionHasProperty(after, propName))
        deleteProperty(propName)
      else
        addProperty(propName);
    }
    return builder.giveQuestionPropertyNames(after.QuestionType)
      .map<MenuItem>(p => {
        return ({
          title: p,
          value: createOnItemClick(p),
          icons: questionHasProperty(after, p) ? [faCheck] : []
        });
      });
  }, [after, builder, addProperty, deleteProperty]);

  return <DropDownMenu
    label={label}
    icon={icon}
    // icon={faPlus}
    disabled={!after}
    menuItems={menuItems}
    align="right"
  />
}