import React, { useCallback, useEffect, useState } from "react";
import { Edge, isEdge, isNode, Node, NodeChange, useReactFlow, useStore as useFlowState, } from "react-flow-renderer";
import { QuestionUnion } from "../../questionnaires/entities/question";
import { useDispatchDiagram, useDispatchQuestionEditor, useDispatchQuestionnaire } from "../hooks/useDispatch";
import { useDiagram } from "../hooks/useSelector";
import { NodeTypeNames } from "./Types";
import { diffPositions } from "../../core/addPositions";

function getNode(el: any): Node | undefined {
  return isNode(el) ? el as Node : undefined;
}

function getEdge(el: Node<any> | Edge<any>): Edge | undefined {
  return isEdge(el) ? el as Edge : undefined;
}

export function useDiagramController() {
  const {
    updateQuestion,
    removeConnection,
    deleteQuestion,
    moveQuestion,
  } = useDispatchQuestionnaire();
  const {setMousePosition, onNodesChange, onEdgesChange} = useDispatchDiagram();
  const {setEditedQuestion} = useDispatchQuestionEditor();

  const {
    nodes, edges,
    questionNodes, questionEdges
  } = useDiagram();
  const [, , zoom] = useFlowState().transform;
  const [editedNode, setEditedNode] = useState<Node<QuestionUnion> | undefined>(undefined);
  const {setCenter, project,} = useReactFlow();

  // const [nodes, setNodes, newQuestionKeys, hookNodes] = useAllNodes(questions);
  // const [hookEdges] = useHookLines(hookNodes);
  // const [edges, setEdges] = useConnectionLines(connections);
  // const allEdges = useMemo(() => [...hookEdges, ...edges], [hookEdges, edges]);
  //
  // const changeNodesSize = useCallback(async (sc: NodeDimensionChange[]) => {
  //   nodes
  //     .filter(n => sc.some(({id}) => n.id === id))
  //     .map<[Node, Size]>(n => ([n, sc.find(({id}) => n.id === id)!.dimensions]))
  //     .filter(([n, dimensions]) => !isSameSize(n.data.__size, dimensions))
  //     .forEach(([n, dimensions]) => {
  //       setSize({__uuid: n.data.__uuid, key: '__size', value: dimensions})
  //     });
  // }, [nodes, setSize]);
  //
  // const onNodesChange = useCallback((changes: NodeChange[]) => {
  //   setNodes((nds) => {
  //     if (changes[0].type === 'dimensions') {
  //       changeNodesSize(changes as NodeDimensionChange[])
  //       return nds;
  //     }
  //     // console.log('node, changes', changes)
  //     return applyNodeChanges(changes, nds);
  //   });
  // }, [setNodes, changeNodesSize]);
  //
  // const onEdgesChange = useCallback((changes: EdgeChange[]) => setEdges((eds) => applyEdgeChanges(changes, eds)),
  //   [setEdges]);

  const editQuestion = useCallback((node: Node<QuestionUnion>) => {
    setEditedQuestion(node.data);
    setEditedNode(node);
  }, [setEditedQuestion, setEditedNode]);

  useEffect(() => {
    if (!editedNode) return;
    const {x, y} = editedNode.position
    setTimeout(() => setCenter(x, y + 200, {zoom, duration: 250}), 10)
    setEditedNode(undefined)
  }, [editedNode, setEditedNode, setCenter, zoom]);
  //
  // useEffect(() => {
  //   if (newQuestionKeys.length) {
  //     setEdges(eds => applyEdgeChanges(eds.map<EdgeSelectionChange>(ed => ({
  //       id: ed.id,
  //       type: "select",
  //       selected: false
  //     })), eds))
  //     setNodes(nds => applyNodeChanges(nds.map<NodeSelectionChange>(n => ({
  //       id: n.id,
  //       type: "select",
  //       selected: newQuestionKeys.includes(n.id)
  //     })), nds));
  //   }
  // }, [newQuestionKeys, setEdges, setNodes, nodes]);

  return {
    nodes,
    edges,
    onNodesChange: useCallback((changes: NodeChange[]) => {
      onNodesChange(changes)
    }, [onNodesChange]),
    onEdgesChange,

    onMouseMove: useCallback((event: React.MouseEvent) => setMousePosition(
        project({x: event.clientX, y: event.clientY})
      )
      , [setMousePosition, project]),

    onNodeDrag: useCallback((event, draggedNode: Node<QuestionUnion>) => {
      if (draggedNode.type === NodeTypeNames.Hook) {
        // moveOtherNode(draggedNode)
      } else {
        const offset = diffPositions(draggedNode.data.__position, draggedNode.position);
        questionNodes.filter(qn => qn.selected)
          .forEach(node => moveQuestion({q: node.data, offset}))
      }
    }, [questionNodes, moveQuestion]),

    onSelectionDragStop: useCallback((event, nodes: Node<QuestionUnion>[]) => {
      nodes.forEach(node =>
        updateQuestion({...node.data!, __position: node.position})
      )
    }, [updateQuestion]),

    onElementsRemove: useCallback(elements => {
      elements.forEach((el: any) => {
        const connection = getEdge(el);
        const node = getNode(el);
        if (node) {
          deleteQuestion(node.data)
          return;
        }
        if (!connection || !connection.sourceHandle || !connection.target) return;
        removeConnection({
          triggerId: connection.sourceHandle,
          targetKey: connection.target,
        })
      })
    }, [removeConnection, deleteQuestion]),

    onDoubleClickNode: useCallback((event, node: Node<QuestionUnion>) => {
      editQuestion(node)
    }, [editQuestion]),

    // here we want to set diagramModel selections, but keep them, when flow loses selection
    // we reset model selection manually through mouse clicks or when a new selection starts
    // any mouse event on model selected nodes should respect the selection, so dragging, right click, delete, anything

    onClickPane: useCallback((event) => {
      setEditedQuestion(undefined);
    }, [setEditedQuestion]),

    onKeyDown: useCallback((event: React.KeyboardEvent) => {
      if (event.code === 'Delete') {
        questionEdges.filter(e => e.source).forEach(edge => removeConnection({
          triggerId: edge.source,
          targetKey: edge.target,
        }));
      }
    }, [questionEdges, removeConnection]),
  }
}