import { FC } from "react";
import "./PropertyPair.scss"

export type PropertyPairProps = {
  valueOne: string | number,
  valueTwo: string | number,
  onChangeOne?: (v: string) => void
  onChangeTwo?: (v: string) => void
  onFocus?: (event?: { currentTarget: EventTarget & HTMLElement }) => void
}

export const PropertyPair: FC<PropertyPairProps> = ({
  valueOne, valueTwo, onChangeOne, onChangeTwo, onFocus
}) => (
  <div
    onFocus={onFocus}
    onClick={onFocus}
    className={`PropertyPair`}
  >
    <input
      key="one"
      value={valueOne}
      type={typeof valueOne === "string" ? "text" : "number"}
      className="InputOne"
      onChange={event => {onChangeOne && onChangeOne(event.currentTarget.value || '')}}
    />
    <input
      key="twop"
      value={valueTwo}
      type={typeof valueTwo === "string" ? "text" : "number"}
      className="InputTwo"
      onChange={event => {onChangeTwo && onChangeTwo(event.currentTarget.value || '')}}
    />
  </div>
)

