import React, { FC, useState } from 'react';
import { If } from "./If";
import { Btn, BtnProps } from "./Btn";
import './DropDownBtn.scss';
import { MenuAlign } from "./PopupMenu";

type Menu = ((props: { onClose: () => void, align: MenuAlign }) => (JSX.Element | null))

interface DropDownBtnProps extends BtnProps {
  className?: string;
  menu: Menu;
  align?: MenuAlign
}

export const DropDownBtn: FC<DropDownBtnProps> = ({menu: M, align = 'left', ...btnProps}) => {
  const [isActive, setIsActive] = useState(false)
  return <div
    className={`DropDownBtn`}
  >
    <Btn
      {...btnProps}
      onClick={() => setIsActive(!isActive)}
    />
    <If condition={isActive}>
      {<M align={align} onClose={() => setIsActive(false)}/>}
    </If>
  </div>;
};
