import { Position, QuestionKey, QuestionUnion } from "../questionnaires/entities/question";
import { createModel } from "@rematch/core";
import { RootModel, RootState } from "./index";
import {
  findUniqueChild,
  mutateUniqueChildOfQuestion,
  PropertyKeyValue,
  UniqueObject
} from "../questionnaires/entities/anyObject";
import { Optional } from "../core/typeHelpers";
import { createTrigger, Trigger } from "../questionnaires/entities/trigger";
import * as _ from "lodash";
import {
  changeKeyReferences,
  convertIncompatibleQuestion,
  ensureHasBlock,
  fixBrokenTriggerKeys,
  fixNonBlockingQuestionPath,
  flattenNestedQuestions,
  getQuestionByKey,
  getQuestionHooks,
  getQuestionWithHook,
  giveAllObjectsUuid,
  giveUniqueKeys,
  giveUniqueNextKey,
  removeQuestionsWithKeys
} from "./questionnaireFunctions";
import { ContentHook, createTriggeredBy, TriggeredBy } from "../questionnaires/entities/triggeredBy";
import { createStateWithHistory, reduceToHistory, undoRedoReducers } from "./undoRedo";
import { addPositions } from "../core/addPositions";
import { sameQuestion } from "./diagramModel";

export type Connection = { triggerId: string, targetKey: string, id: string, sourceKey: string }
export type ConnectionPayload = { triggerId: string, targetKey: string }
type MoveQuestionsPayload = { q: QuestionUnion, offset?: Position, position?: Position }
type ChangeHookParentPayload = { h: TriggeredBy, qKey: string }

export type ChangeKeyReferences = {
  before: QuestionKey,
  after: QuestionKey,
}

export type ChangeKeyPrefixes = {
  before: string,
  after: string,
  qs: QuestionUnion[]
}

export interface QuestionnaireState {
  questions: QuestionUnion[],
}

const initialState: QuestionnaireState = {
  questions: [],
};

type AddQuestionPayload = { question: QuestionUnion, position?: Position, workflowId?: string, callback?: (q: QuestionUnion) => void };
type DuplicateQuestionsPayload = { questions: QuestionUnion[], offset?: Position, workflowId: string };

export function convertJsonDataToQuestionnaire(questions: QuestionUnion[]): QuestionUnion[] {
  let converted = flattenNestedQuestions(questions)
    .map(q => convertIncompatibleQuestion(q))
    .map(q => fixBrokenTriggerKeys(q))
    .map(q => fixNonBlockingQuestionPath(q))

  // ensure all Keys are unique
  giveAllObjectsUuid(converted);
  return giveUniqueKeys(converted)
    .map((q, i) => ({
      ...q,
      __position: q.__position || {x: -9999, y: -9999} //{x: 300, y: 200 + i*200}
    }))
}

export const questionnaireModel = createModel<RootModel>()({
  name: 'questionnaireModel',
  state: createStateWithHistory(initialState),
  reducers: {
    set: (state, payload: Optional<QuestionnaireState>) => reduceToHistory(state, {...state.present, ...payload}),
    reset: (state) => reduceToHistory(state, initialState),

    // replaces all the q data, so to open or paste q complete questionnaire set
    replaceQuestions: (state, questions: QuestionUnion[]) => {
      const converted = convertJsonDataToQuestionnaire(questions);
      return reduceToHistory(state, {...state.present, questions: converted})
    },

    addQuestion: (state, {question}: AddQuestionPayload) => {
      // const question = QuestionFactory.create(type, {__position: position, __workflowId: workflowId});
      question.Key = giveUniqueNextKey(state.present.questions, question.Key);
      return reduceToHistory(state, {
        ...state.present,
        questions: state.present.questions.concat(question)
      });
    },
    addUniqueQuestions: (state, questions: QuestionUnion[]) =>
      reduceToHistory(state, {
        ...state.present,
        questions: state.present.questions.concat(questions)
      }),
    deleteQuestion: (state, question: QuestionUnion) =>
      reduceToHistory(state, {
        ...state.present,
        questions: removeQuestionsWithKeys([question.Key], state.present.questions)
      }),
    deleteQuestionsByKeys: (state, keys: string[]) =>
      reduceToHistory(state, {
        ...state.present,
        questions: removeQuestionsWithKeys(keys, state.present.questions)
      }),
    // somewhat brutal. don't use for Key changes, use changeKeyReferences for that
    updateQuestion: (state, q: QuestionUnion | QuestionUnion[]) => {
      const qs = _.castArray(q);
      return reduceToHistory(state, {
        ...state.present,
        questions: _.unionWith(qs, state.present.questions, sameQuestion)
      });
    },
    changeKeyReferences: (state, {before, after}: ChangeKeyReferences) => reduceToHistory(state, {
      ...state.present,
      questions: changeKeyReferences(state.present.questions, before, after)
    }),
    moveQuestion: (state, {q, offset, position}: MoveQuestionsPayload) => {
      const ori = state.present.questions.find(o => sameQuestion(o, q))!;
      const newPos = offset ? addPositions(offset, ori.__position) : position;
      return reduceToHistory(state, {
        ...state.present,
        questions: state.present.questions.map(qa => sameQuestion(ori, qa)
          ? {
            ...ori,
            __position: newPos
          }
          : qa
        )
      });
    },


    // some things should just not create history to undo, like size
    updateQuestionNoHistory: (state, after: QuestionUnion) => {
      state.present.questions = state.present.questions.map(q => q.__uuid === after.__uuid ? after : q)
    },

    ...undoRedoReducers<QuestionnaireState>(),
  },
  effects: (dispatch) => ({
    set: (x?: Optional<QuestionnaireState>, s?: RootState) => {
      dispatch.diagramModel.replaceQuestionNodes(s!.questionnaireModel?.present.questions)
      dispatch.questionEditorModel.setEditedQuestion(undefined);
    },
    reset: (x?: undefined, s?: RootState) => {
      dispatch.diagramModel.replaceQuestionNodes(s!.questionnaireModel?.present.questions)
      dispatch.questionEditorModel.setEditedQuestion(undefined);
    },
    replaceQuestions: (x: QuestionUnion[], s: RootState) => {
      dispatch.diagramModel.replaceQuestionNodes(s.questionnaireModel?.present.questions)
      dispatch.questionEditorModel.setEditedQuestion(undefined);
    },
    deleteQuestion: (q: QuestionUnion) => {
      dispatch.diagramModel.deleteQuestionNodesByKeys([q.Key])
      dispatch.questionEditorModel.refreshEdited()
    },
    deleteQuestionWithKeys: (keys: string[]) => {
      dispatch.diagramModel.deleteQuestionNodesByKeys(keys)
      dispatch.questionEditorModel.refreshEdited()
    },
    updateQuestion: (q: QuestionUnion | QuestionUnion[]) => {
      dispatch.diagramModel.updateQuestionNodes(_.castArray(q));
      dispatch.questionEditorModel.refreshEdited()
    },
    updateQuestionNoHistory: (q: QuestionUnion | QuestionUnion[]) => {
      dispatch.diagramModel.updateQuestionNodes(_.castArray(q));
    },
    undo: (x?: undefined, s?: RootState) => {
      dispatch.diagramModel.replaceQuestionNodes(s!.questionnaireModel?.present.questions)
      dispatch.questionEditorModel.refreshEdited();
    },
    redo: (x?: undefined, s?: RootState) => {
      dispatch.diagramModel.replaceQuestionNodes(s!.questionnaireModel?.present.questions)
      dispatch.questionEditorModel.refreshEdited();
    },
    addUniqueQuestions: (qs: QuestionUnion[]) => {
      dispatch.diagramModel.updateQuestionNodes(qs);
    },
    changeKeyReferences: (x?: ChangeKeyReferences, s?: RootState) => {
      dispatch.diagramModel.replaceQuestionNodes(s!.questionnaireModel?.present.questions);
      dispatch.questionEditorModel.refreshEdited();
    },
    changeKeyPrefixes: ({before, after, qs}: ChangeKeyPrefixes, state) => {
      const questions = qs
        .map(q => ([q.Key, q.Key.replace(before, after)]))
        .reduce<QuestionUnion[]>((newQS, [before, after]) => changeKeyReferences(newQS, before, after), state.questionnaireModel.present.questions);
      dispatch.questionnaireModel.updateQuestion(questions)
    },

    // this effect is called from QuestionEditorModel, it should not call reducers or effects that call that Model
    applyEditedQuestion: (_?: undefined, state?: RootState) => {
      const {after} = state!.questionEditorModel;
      if (!after) return;
      dispatch.questionnaireModel.updateQuestion(after);
    },

    // value = one object with a __uuid that exists anywhere in the questionnaire
    changeItemValues: (value: UniqueObject, s: RootState) => {
      const q = mutateUniqueChildOfQuestion(s.questionnaireModel.present.questions, value);
      if (q)
        dispatch.questionnaireModel.updateQuestion(q);
    },
    changeItemValuesNoHistory: (value: UniqueObject, s: RootState) => {
      const q = mutateUniqueChildOfQuestion(s.questionnaireModel.present.questions, value);
      if (q)
        dispatch.questionnaireModel.updateQuestionNoHistory(q);
    },

    // these are called from diagram
    addQuestion: ({callback}: AddQuestionPayload, s?: RootState) => {
      const [q] = s!.questionnaireModel.present.questions.slice(-1)
      dispatch.diagramModel.updateQuestionNodes([q]);
      console.log('after add question', q, callback)
      callback && callback(q);
    },
    duplicateQuestions: ({
      questions,
      offset = {x: 200, y: 50},
      workflowId
    }: DuplicateQuestionsPayload, s: RootState) => {
      let duplicates = _.cloneDeep(questions).map(q => {
        q.__position = {
          x: (q.__position?.x || 0) + offset?.x,
          y: (q.__position?.y || 0) + offset?.y
        }
        q.__workflowId = workflowId;
        return q
      })
      giveAllObjectsUuid(duplicates, true);
      const keys = duplicates.map(q => q.Key);
      // remove any connection outside of the duplicates
      duplicates.forEach(q => {
        q.Triggers?.forEach(tr => tr.QuestionKeys = tr.QuestionKeys?.filter(key => keys.includes(key)))
      })
      keys.forEach(key => {
        const refList = s.questionnaireModel?.present.questions.concat(duplicates);
        duplicates = changeKeyReferences(duplicates, key, giveUniqueNextKey(refList, key));
      });
      dispatch.questionnaireModel.addUniqueQuestions(duplicates);
      dispatch.questionEditorModel.refreshEdited();
    },

    addTrigger: (questionUuid: string, s: RootState) => {
      const q = s.questionnaireModel.present.questions
        .find((q: QuestionUnion) => q.__uuid === questionUuid);
      if (!q) return;
      // ignore  no-mixed-operators
      const {TriggerType} = q.Triggers && q.Triggers.length && q.Triggers[0] || {};
      const trigger: Trigger = createTrigger(TriggerType)
      dispatch.questionnaireModel.updateQuestion({
        ...q,
        Triggers: [...q.Triggers || [], trigger]
      })
    },
    addHook: (questionUuid: string, s: RootState) => {
      const q = s.questionnaireModel.present.questions
        .find((q: QuestionUnion) => q.__uuid === questionUuid);
      if (!q) return;
      const coreTriggerBys = q.TriggeredBy && getQuestionHooks(q) || [];
      const Content = _.cloneDeep<ContentHook>(coreTriggerBys.length && coreTriggerBys[0].Content || []);
      const triggeredBy: TriggeredBy[] = createTriggeredBy({Content})
      dispatch.questionnaireModel.updateQuestion({
        ...q,
        TriggeredBy: [...(q.TriggeredBy || []), triggeredBy],
        SideEffect: ensureHasBlock(q.SideEffect),
      } as QuestionUnion)
    },
    removeHook: (questionUuid: string, s: RootState) => {
      const q = s.questionnaireModel.present.questions
        .find((q: QuestionUnion) => q.__uuid === questionUuid);
      if (!q) return;
      dispatch.questionnaireModel.updateQuestion({
        ...q,
        TriggeredBy: undefined
      })
    },

    addConnection: (con: ConnectionPayload, s: RootState) => {
      console.log('add connection', con)
      const {questions} = s.questionnaireModel?.present;
      if (!questions) return;
      const item = findUniqueChild(questions, con.triggerId) as UniqueObject;
      if (!item) return;
      dispatch.questionnaireModel.changeItemValues({
        ...item,
        QuestionKeys: _.union(item.QuestionKeys, [con.targetKey]),
      });

    },
    removeConnection: (con: ConnectionPayload, state: RootState) => {
      console.log('remove connection??', con)
      const {questions} = state.questionnaireModel?.present;
      if (!questions) return;
      const item = findUniqueChild(questions, con.triggerId) as Trigger;
      if (!item) return;
      console.log('remove connection!', con)
      dispatch.questionnaireModel.changeItemValues({
        ...item,
        QuestionKeys: item.QuestionKeys?.filter(k => k !== con.targetKey),
      });
    },

    changeHookParent: ({h, qKey}: ChangeHookParentPayload, s: RootState) => {
      let qA = getQuestionByKey(s.questionnaireModel?.present.questions, qKey);
      if (!qA) return;
      let qB = getQuestionWithHook(s.questionnaireModel?.present.questions, h.__uuid);
      console.log('change hook parent', h, qB)
      if (!qB) return;
      // move from previous parent to new or swap if both a parent
      qB = _.cloneDeep(qB) as QuestionUnion;
      if (qA.TriggeredBy)
        qB.TriggeredBy = _.cloneDeep(qA.TriggeredBy);
      else
        delete qB.TriggeredBy;
      // add to new
      qA = _.cloneDeep(qA) as QuestionUnion;
      qA.TriggeredBy = [[_.cloneDeep(h)]];
      dispatch.questionnaireModel.updateQuestion([qA, qB]);
    },

    // this version differs from the questionEditorModel in that it does not apply a template for the changed property
    // so use it for props that should not affect question structure or apply it manually.
    // also it might be a bit slower as it has to search the whole questionnaire.
    changeItemPropertyValue: (prop: PropertyKeyValue, state: RootState) => {
      const {questions} = state.questionnaireModel?.present;
      if (!prop.__uuid || !questions) return;
      const q = questions.find(q => q.__uuid === prop.__uuid);
      if (q) {
        dispatch.questionnaireModel.updateQuestion({
          ...q, [prop.key]: prop.value
        });
      } else {
        const item = {
          ...findUniqueChild(questions, prop.__uuid) as UniqueObject,
          [prop.key]: prop.value,
        };
        dispatch.questionnaireModel.changeItemValues(item);
      }
    },
    mergeItemValues: (values: UniqueObject, state: RootState) => {
      console.log('merge values', values)
      const {questions} = state.questionnaireModel?.present;
      if (!values.__uuid || !questions) return;
      const item = {
        ...findUniqueChild(questions, values.__uuid) as UniqueObject,
        ...values
      };
      dispatch.questionnaireModel.changeItemValues(item);
    },

    //setting size will create a lot of non-visual unneeded undo steps, so lets skip history for it
    setSize: (prop: PropertyKeyValue, state: RootState) => {
      const {questions} = state.questionnaireModel?.present;
      if (!prop.__uuid || !questions) return;
      const q = questions.find(q => q.__uuid === prop.__uuid);
      if (q) {
        dispatch.questionnaireModel.updateQuestionNoHistory({
          ...q, [prop.key]: prop.value
        });
      } else {
        const item = {
          ...findUniqueChild(questions, prop.__uuid) as UniqueObject,
          [prop.key]: prop.value,
        };
        dispatch.questionnaireModel.changeItemValuesNoHistory(item);
      }
    },
  }),
  selectors: (slice, createSelector, hasProps) => ({
    questionKeys() {
      return slice((questionnaire) => {
          return questionnaire.present.questions.map(q => q.Key);
        }
      )
    },
  })
});
