import * as React from 'react';
import { ReactElement } from 'react';
import { If } from "./If";
import { classNamesProp, DivProps } from "./CoreProp";
import './Label.scss'

export interface LabelProps extends DivProps {
  text?: string;
  className?: string;
}

export function Label({text, className, children, ...props}: LabelProps): ReactElement | null {
  return <If condition={text}>
    <div {...classNamesProp('Label', className || '')} {...props}>{text}{children}</div>
  </If>;
}
