import './Property.scss';
import { useEffect, useMemo, useRef } from "react";
import { QuestionKey } from "../../../questionnaires/entities/question";
import { PropertyKeyValue } from "../../../questionnaires/entities/anyObject";
import _ from 'lodash';
import { usePropState } from "../../hooks/usePropState";
import autosize from "autosize";

type PropertyProps = {
  name: QuestionKey
  label?: string | JSX.Element
  value: any
  type?: 'string' | 'number' | 'values' | undefined
  onChange?: (p: PropertyKeyValue) => void
  onFocus?: (event?: { currentTarget: EventTarget & HTMLElement }) => void
  changeOnBlurOnly?: boolean
  disabled?: boolean
}

function prettyJson(val: any): string {
  return JSON.stringify(val, null, 2)
}

export const Property: React.FunctionComponent<PropertyProps> = (
  {
    name,
    label,
    value,
    type,
    onFocus,
    onChange = undefined,
    changeOnBlurOnly = false,
    disabled = false,
  }) => {
  const isJson = useMemo(() => !type && !['string', 'number'].includes(typeof value), [type, value]);

  const legalValue = useMemo<string | number>(() => isJson
      ? prettyJson(value) : type === 'values' ? JSON.stringify(value) : value
    , [value, isJson, type]);
  const [edit, setEdit] = usePropState<string | number>(legalValue);
  const ref = useRef<HTMLTextAreaElement>(null)

  const validValue = useMemo<string | number | {}>(() => {
    try {
      return type === undefined || type === 'values' ? JSON.parse(edit as string) : edit;
    } catch (e: any) {
      return undefined
    }
  }, [edit, type]);

  const isValid = useMemo(() => {
    return validValue !== undefined
  }, [validValue]);

  useEffect(() => {
    if (changeOnBlurOnly || validValue === undefined || _.isEqual(validValue, value)) return;
    onChange && onChange({key: name, value: validValue});
  }, [name, value, validValue, onChange, changeOnBlurOnly]);

  useEffect(() => {
    if (ref.current)
      autosize(ref.current)
  }, [ref, edit]);

  const onBlur = () => {
    if (changeOnBlurOnly) {
      if (validValue === undefined || _.isEqual(validValue, value)) return;
      onChange && onChange({key: name, value: validValue});
    } else if (!isValid) {
      setEdit(prettyJson(value,));
    }
  }

  return (
    <div
      onFocus={onFocus}
      onClick={onFocus}
      className={`Property${!type ? ' json' : ''}`}
    >
      {label !== '' ? <span>{label || name}</span> : null}
      {isJson
        ? <textarea
          ref={ref}
          style={{color: isValid ? 'white' : 'orangered'}}
          value={edit}
          onChange={event => {
            setEdit(event.target.value);
          }}
          onBlur={onBlur}
        />
        : <input
          style={{color: isValid ? 'white' : 'orangered'}}
          value={edit}
          inputMode={type === 'number' ? 'numeric' : 'text'}
          onChange={event => {
            setEdit(event.target.value);
          }}
          onBlur={onBlur}
          disabled={disabled}
        />
      }
    </div>
  );
}
