import { PageName } from "./pages";
import { createUniqueObject, UniqueObject } from "./anyObject";

export const SideEffectTypes = ["Block", "Cancel", "GoTo"] as const;
export type SideEffectType = typeof SideEffectTypes[number];

export type SideEffect = UniqueObject & {
  SideEffectType: SideEffectType;
  Value?: PageName | undefined;
}

export const createSideEffect = (type: SideEffectType | string = 'Block'): SideEffect => createUniqueObject({
  SideEffectType: type as SideEffectType,
})