import GivePropertyComponent from "./PropertyEditor";
import React, { PropsWithChildren } from "react";
import { useBuilder } from "../hooks/useBuilder";
import { ComparisonLogic } from "../../questionnaires/entities/comparison";
import { PropertyKeyValue } from "../../questionnaires/entities/anyObject";

interface LogicComparisonComponentProps {
  logicComparison: ComparisonLogic,
  onChange: (payload: PropertyKeyValue) => void,
}

export const LogicComparisonComponent = (
  {
    logicComparison,
    onChange,
  }: PropsWithChildren<LogicComparisonComponentProps>) => {
  const builder = useBuilder();

  if (!logicComparison) return null;

  return <div>
    <GivePropertyComponent
      name="Selector"
      label="List Selector"
      value={logicComparison.Selector || []}
      options={builder.getPropertyValueOptions('Selector')}
      onChange={onChange}
    />
    <GivePropertyComponent
      name="Comparator"
      label="Logic Comparator"
      value={logicComparison.Comparator}
      options={builder.getPropertyValueOptions('LogicComparator')}
      onChange={onChange}
    />
  </div>
}