import { Position, Size } from "../questionnaires/entities/question";

export function addPositions(pos1: Position = {x: 0, y: 0}, pos2: Position = {x: 0, y: 0}): Position {
  return {
    x: pos1.x + pos2.x,
    y: pos1.y + pos2.y
  }
}

export function diffPositions(pos1: Position = {x: 0, y: 0}, pos2: Position = {x: 0, y: 0}): Position {
  return {
    x: pos2.x - pos1.x,
    y: pos2.y - pos1.y
  }
}

export const isSameSize = (
  pos1: Size = {width: 0, height: 0},
  pos2: Size = {width: 0, height: 0}
): boolean => pos2.width - pos1.width === 0 && pos2.height - pos1.height === 0
