import { store } from "../../store/store";
import { useSelector } from "react-redux";
import { RootState } from "../../models";
import { useDiagram } from "./useSelector";
import { useDispatchDiagram } from "./useDispatch";
import { useMemo } from "react";

const workflowsSelector = store.select.diagramModel.workflows;

export function useWorkflows() {
  const workflows = useSelector((state: RootState) => workflowsSelector(state));
  const {currentWorkflowId} = useDiagram();
  const {setWorkflow} = useDispatchDiagram();
  const currentWorkflow = useMemo(() => workflows.find(w => w.info.__uuid === currentWorkflowId) || workflows[0], [workflows, currentWorkflowId]);
  return {
    workflows,
    setWorkflow,
    currentWorkflow,
    currentWorkflowId
  }
}