import './QuestionItem.scss';
import "./GenericObjectComponent.scss";
import { makeKeyValueList, PropertyKeyValue } from "../../questionnaires/entities/anyObject";
import { useQuestionEditor } from "../hooks/useSelector";
import { PropertyName } from "../../questionnaires/factories/propertyBuilder";
import { RequestParam, RequestType } from "../../questionnaires/entities/request";
import { useDispatchQuestionEditor, useDispatchQuestionnaire } from "../hooks/useDispatch";
import { useCallback } from "react";

const orderOfProps: PropertyName[] = [
  "Key", 'QuestionType', "Title", "Icon", "WhenToShow", "InputType", "DontSend",
  "Message", "SideEffect",
  "TriggeredBy",
  "MessageSelector",
  "FunctionSelector",
  "RequestType", "RequestParams",
  "Options", "OptionsSelector", "AnswerSelector", "AnswerValue",
  "Comparisons", "FunctionArguments",
  "Triggers"
]

export interface RequestProps {
  requestType: RequestType,
  requestParams: [RequestParam],
}

export function useOrderedProperties(): PropertyKeyValue[] {
  const {after} = useQuestionEditor();
  if (!after) return []
  return makeKeyValueList(after)
    .sort((a, b) =>
      orderOfProps.indexOf(a.key) - orderOfProps.indexOf(b.key)
    )
    .reduce<PropertyKeyValue[]>((props, p) => {
      if (p.key === 'RequestType') {
        props.push({
          key: 'Request',
          value: {
            requestType: after.RequestType,
            requestParams: after.RequestParams,
          } as RequestProps
        })
      } else if (p.key === 'RequestParams') {
        // do nothing
      } else {
        props.push(p);
      }

      return props
    }, []);
}

export function useQuestionState() {
  const {after, currentProperty} = useQuestionEditor();
  if (!after) return {};

  const {QuestionType, Key} = after;
  return {currentProperty, item: after, QuestionType, Key}
}


export function useQuestionHandlers() {
  const {changePropertyValue, setCurrentProperty,} = useDispatchQuestionEditor();
  const {currentProperty} = useQuestionEditor()
  const {changeKeyReferences, updateQuestion} = useDispatchQuestionnaire()
  const {Key} = useQuestionEditor()?.after || {Key: "Empty"}

  return {
    updateQuestion,
    onChangeValue: (payload: PropertyKeyValue) => {
      return changePropertyValue(payload);
    },
    onKeyPropChange: (p: PropertyKeyValue) => {
      changeKeyReferences({before: Key, after: p.value as string})
    },
    onPropertyClick: useCallback((key: string) => {
      if (currentProperty === key) return;
      if (["Request", "Key", "QuestionType"].includes(key))
        setCurrentProperty(undefined);
      else
        setCurrentProperty(key);
    }, [setCurrentProperty, currentProperty]),
  }
}
