import { Selector } from "./selector"
import { createUniqueObject, UniqueObject } from "./anyObject";

export type QuestionOption = UniqueObject & {
  Title: string;
  Value: number;
}

export const createOption = (): QuestionOption => createUniqueObject({
  Title: '',
  Value: 0
})

export type OptionsSelector = UniqueObject & {
  Selector: Selector,
  Mapping: QuestionOption,
}

export const createOptionsSelector = (): OptionsSelector => createUniqueObject({
  Selector: ["answer", ""],
  Mapping: createOption(),
});
