import React, { useCallback, useRef, useState } from 'react';
import * as _ from "lodash";

export function usePropState<T>(defaultValue: T): [T, React.Dispatch<React.SetStateAction<T>>] {
  const [value, setValue] = useState(defaultValue)
  const [def, setDef] = useState(defaultValue)

  if (!_.isEqual(defaultValue, def)) {
    setValue(defaultValue);
    setDef(defaultValue);
  }
  return [value, setValue];
}

export function useNestedState<T>(anyThing: T): [T, (newThing: T) => void] {
  const internal = useRef<T | null>(null)

  if (!_.isEqual(internal.current, anyThing)) {
    internal.current = anyThing;
  }

  const setState = useCallback((newThing: T) => {
    if (_.isEqual(internal.current, newThing)) return;
    internal.current = newThing;
  }, [])

  return [internal.current!, setState];
}