import { useCallback, useState } from "react";
import "./GenericObjectComponent.scss";
import { KeyValue, PropertyKeyValue } from "../../questionnaires/entities/anyObject";
import { GenericObjectComponentProps } from "./GenericObjectComponent";
import * as _ from "lodash";
import { useDispatch } from "react-redux";
import { Dispatch } from "../../models";
import { usePropertyList } from "../hooks/usePropertyList";
import { useBuilder } from "../hooks/useBuilder";
import { GetPropertyValueOptionsType } from "../../questionnaires/factories/propertyBuilder";

interface GenericObjectComponentUses {
  getOptions: GetPropertyValueOptionsType;
  onChange: ({key, value}: PropertyKeyValue) => void;
  propertyList: KeyValue<any>[];
}

export function useObjectTransformer(
  {
    item,
    propName = ''
  }: GenericObjectComponentProps
): GenericObjectComponentUses {
  if (!item) throw new Error("Item cannot be undefined!")
  const [internal, setInternal] = useState(item);
  const {changeItemPropertyValue} = useDispatch<Dispatch>().questionEditorModel

  if (!_.isEqual(internal, item)) {
    setInternal({...item});
  }
  const builder = useBuilder();
  const propertyList = usePropertyList(internal, propName);

  const onChange = useCallback(
    (p: PropertyKeyValue) => changeItemPropertyValue({...p, __uuid: item.__uuid})
    , [changeItemPropertyValue, item.__uuid]);

  return {
    getOptions: builder.getPropertyValueOptions,
    onChange,
    propertyList,
  }
}
