import { createSelector, Selector } from "./selector";
import { createUniqueObject, UniqueObject } from "./anyObject";
import { PropertyTemplateBuilder } from "../factories/propertyBuilder";

type Value = number | string | boolean | number[] | string[] | boolean[];

export const LogicComparators = ['OR', 'AND', 'ANY', 'ALL'] as const;
export const ValueComparators = ['==', '===', '>', '<', '>=', '<=', '!=', '!==', 'IN'] as const;
export const Comparators = [...ValueComparators, ...LogicComparators] as const;

type LogicComparator = typeof LogicComparators[number];
type ValueComparator = typeof ValueComparators[number];
type LeftHand = {
    LeftHandComparison?: Comparison,
    LeftHandSelector?: Selector,
    LeftHandValue?: Value
};
type RightHand = {
    RightHandComparison?: Comparison,
    RightHandSelector?: Selector,
    RightHandValue?: Value
};

export type ComparisonLogic = UniqueObject & {
    Comparator: LogicComparator,
    Selector?: Selector,
    Comparisons: Comparison[]
}
export type ComparisonValue = UniqueObject & { Comparator: ValueComparator } & LeftHand & RightHand;
export type Comparison = ComparisonLogic | ComparisonValue;
export type ComparisonKey = keyof Comparison;

export const createValueComparison = (): ComparisonValue => createUniqueObject({
    LeftHandSelector: createSelector(),
    Comparator: "===",
    RightHandValue: [0],
});

export const createLogicComparison = (): ComparisonLogic => createUniqueObject({
    Comparator: "AND",
    Selector: undefined,
    Comparisons: [createValueComparison()]
});

export const comparisonTemplateBuilder: PropertyTemplateBuilder = (prop) => {
    const {key, value} = prop;
    if (['LeftHand', 'RightHand'].find(k => key.includes(k))) {
        return ["Selector", "Value", "Comparison", ""].map(variant => {
            const newKey = key.split('Hand')[0] + 'Hand' + variant;
            return ({
                key: newKey,
                value: newKey === key
                  ? value
                  : undefined
            });
        })
    }
}
