import { useIsMac } from "./useIsMac";
import { useMemo } from "react";

export const useOsHotkeyCombos = () => {
  const isMac = useIsMac();
  return useMemo(() => ({
    cmd: isMac ? 'cmd' : 'ctrl',
    alt: isMac ? 'option' : 'alt',
    shift: 'shift'
  }), [isMac]);
};

export const useOsSelectionKey = () => {
  const isMac = useIsMac();
  return useMemo(() => ({
    add: isMac ? 'Meta' : 'Control',
    subtract: 'Alt',
    drag: 'Shift',
  }), [isMac]);
};