import {
  applyEdgeChanges,
  applyNodeChanges,
  Edge,
  EdgeChange,
  EdgeSelectionChange,
  getConnectedEdges,
  getOutgoers,
  getRectOfNodes,
  Node,
  NodeChange,
  NodeDimensionChange,
  NodeSelectionChange,
} from "react-flow-renderer";
import { RootModel, RootState } from "./index";
import { createModel } from "@rematch/core";
import { Position, QuestionUnion, Size } from "../questionnaires/entities/question";
import * as _ from "lodash";
import { addPositions, isSameSize } from "../core/addPositions";
import { getKeyPrefix, TriggeredBy } from "../questionnaires/entities/triggeredBy";
import { createConnections, getHooksWithQuestion } from "./questionnaireFunctions";
import { Connection } from "./questionnaireModel";
import { Workflow } from "../components/Worksheets/Workflows";

export enum MutationType {
  add, remove, replace
}

export type NodeSelectionOptions = {
  selection: Node<QuestionUnion>[],
  mutation?: MutationType,
}

export type EdgeSelectionOptions = {
  selection: Edge[],
  mutation?: MutationType,
}

const rectOfNodes = (nodes: Node<QuestionUnion>[]) => nodes.length > 1
  ? getRectOfNodes(nodes)
  : {...nodes[0].position, width: 250, height: 50};

function getOffsetFromNodes(nodes: Node<QuestionUnion>[], position?: Position): Position {
  if (!nodes || !nodes.length) return {x: 0, y: 0}
  const {x, y, width, height} = rectOfNodes(nodes);
  return position
    ? {
      x: position.x - x - width / 2,
      y: position.y - y - height / 2,
    }
    : {x: width + 100, y: 0}
}

export const sameThing = (a: { id: string }, b: { id: string }) => a.id === b.id

export const uniqThings = <T extends { id: string }>(things: T[]): T[] => _.uniqWith<T>(things, sameThing);

export interface DiagramState {
  questionNodes: Node<QuestionUnion>[];
  questionEdges: Edge[];
  hookNodes: Node<TriggeredBy[]>[];
  hookEdges: Edge[];
  newNodes: Node<QuestionUnion>[],

  copiedNodes?: Node<QuestionUnion>[];
  contextMenuVisible: boolean;
  currentWorkflowId: string;
  currentMousePosition: Position;
}

const initialState: DiagramState = {
  questionNodes: [],
  questionEdges: [],
  hookNodes: [],
  hookEdges: [],
  newNodes: [],

  copiedNodes: undefined,
  contextMenuVisible: false,
  currentWorkflowId: 'all',
  currentMousePosition: {x: 100, y: 100},
}

export type EventPosition = Position | 'pointer';

const createQuestionNode = (q: QuestionUnion): Node<QuestionUnion> => ({
  id: q.Key,
  type: 'question',
  data: q,
  position: q.__position || {x: 100, y: 100},
  ...q.__size
});

const createQuestionEdge = (con: Connection): Edge => ({
  id: con.id,
  type: 'connection',
  source: con.sourceKey,
  sourceHandle: con.triggerId,
  target: con.targetKey,
  targetHandle: con.targetKey,
  // selected: true
})

function getQuestionUuids(questions: QuestionUnion[]): string[] {
  return questions.map(q => q.__uuid)
}

export function sameQuestions(a: QuestionUnion[], b: QuestionUnion[]): boolean {
  return _.isEqual(getQuestionUuids(a), getQuestionUuids(b));
}

export function sameQuestion({__uuid: a}: QuestionUnion, {__uuid: b}: QuestionUnion): boolean {
  return _.isEqual(a, b);
}

const hookHeightOffset = 80;
export const getHookNodeOffset = (q: QuestionUnion, h: TriggeredBy) => ({
  x: ((q.__size?.width || 50) / 2) - ((h.__size?.width || 200) / 2),
  // y: ((q.__size?.height || 50) / -2) - ((h.__size?.height || 200) / 2),
  y: ((h.__size?.height || 200) * -1) - hookHeightOffset,
})
const createHookNode = (qh: QuestionUnion, hs: TriggeredBy[]): Node<TriggeredBy[]> => ({
  id: `hook_${qh.__uuid}`,
  type: 'hook',
  data: hs,
  parentNode: qh.Key,
  draggable: false,
  position: getHookNodeOffset(qh, hs[0]),
  ...hs[0].__size,
})

const createHookEdge = (qh: QuestionUnion, h: TriggeredBy): Edge => ({
  id: `hook_${qh.__uuid}`,
  type: 'smoothstep',
  source: `hook_${qh.__uuid}`,
  target: qh.Key,
})

const createHookNodes = (questions: QuestionUnion[]) => getHooksWithQuestion(questions)
  .map(({q, h}) => createHookNode(q, h))

const createHookEdges = (questions: QuestionUnion[]) => getHooksWithQuestion(questions)
  .map(({q, h}) => createHookEdge(q, h[0]))

export const diagramModel = createModel<RootModel>()({
  name: 'diagramModel',
  state: initialState,
  reducers: {
    onNodesChange: (state, changes: NodeChange[]) => {
      state.questionNodes = applyNodeChanges(changes, state.questionNodes);
      if (changes.some(c => c.type === 'select')) {
        const selected = state.questionNodes.filter(n => n.selected);
        const connectedEdges = getConnectedEdges(selected, state.questionEdges)
        state.questionEdges = applyEdgeChanges(state.questionEdges
            .map<EdgeSelectionChange>(e => ({id: e.id, type: 'select', selected: connectedEdges.includes(e)}))
          , state.questionEdges);
      }
    },
    onEdgesChange: (state, changes: EdgeChange[]) => {
      state.questionEdges = applyEdgeChanges(changes, state.questionEdges);
    },

    replaceQuestionNodes: (state, questions: QuestionUnion[]) => {
      state.newNodes = [];
      state.questionNodes = questions.map(createQuestionNode);
      state.questionEdges = createConnections(questions).map(createQuestionEdge);
      state.hookNodes = createHookNodes(questions);
      state.hookEdges = createHookEdges(questions);
    },
    updateQuestionNodes: (state, questions: QuestionUnion[]) => {
      const updated = questions.map(createQuestionNode);
      state.newNodes = _.differenceWith(updated, state.questionNodes, sameThing);
      state.questionNodes = _.unionWith(updated, state.questionNodes, sameThing);
      state.questionEdges = createConnections(state.questionNodes.map(n => n.data)).map(createQuestionEdge);
      const deletedHookIds = questions.filter(q => !q.TriggeredBy?.length).map(q => `hook_${q.__uuid}`)
      state.hookNodes = _.unionWith(createHookNodes(questions), state.hookNodes, sameThing).filter(hn => !deletedHookIds.includes(hn.id));
      state.hookEdges = _.unionWith(createHookEdges(questions), state.hookEdges, sameThing).filter(hn => !deletedHookIds.includes(hn.id));

      if (!_.isEmpty(state.newNodes)) {
        state.questionNodes = state.questionNodes.map(n => ({
          ...n, selected: state.newNodes.some(nn => sameThing(n, nn))
        }));
      }
    },
    deleteQuestionNodesByKeys: (state, keys: string[]) => {
      state.hookNodes = state.hookNodes.filter(hn => !keys.includes(hn.parentNode || ''));
      state.questionNodes = state.questionNodes.filter(n => !keys.includes(n.id));
    },

    showContextMenu: (state, show: boolean) => {
      state.contextMenuVisible = show;
    },
    copyNodes: (state, nodes?: Node<QuestionUnion>[]) => {
      state.copiedNodes = nodes || selectedNodes(state);
    },
    duplicateNodes: (state, position?: EventPosition) => {
      state.copiedNodes = selectedNodes(state);
    },
    setWorkflow: (state, workflowId: string) => {
      state.currentWorkflowId = workflowId;
    },
    setMousePosition: (state, pos: Position) => {state.currentMousePosition = pos},
  },
  effects: (dispatch) => ({
    init: (x?: undefined, s?: RootState) => {
      dispatch.diagramModel.replaceQuestionNodes(s!.questionnaireModel?.present.questions)
    },
    selectNodes: (ids: string[], s: RootState) => {
      dispatch.diagramModel.onNodesChange(ids.map(id => ({id, type: 'select', selected: true} as NodeSelectionChange)))
    },

    onNodesChange: (changes: NodeChange[], s: RootState) => {
      const dc = changes.filter(c => c.type === 'dimensions') as NodeDimensionChange[];
      s.diagramModel?.questionNodes
        .filter(n => dc.some(({id}) => n.id === id))
        .map<[Node, Size]>(n => ([n, dc.find(({id}) => n.id === id)!.dimensions]))
        .filter(([n, dimensions]) => !isSameSize(n.data.__size, dimensions))
        .forEach(([n, dimensions]) => {
          dispatch.questionnaireModel.setSize({__uuid: n.data.__uuid, key: '__size', value: dimensions})
        });
      s.diagramModel?.hookNodes
        .filter(n => dc.some(({id}) => n.id === id))
        .map<[Node, Size]>(n => ([n, dc.find(({id}) => n.id === id)!.dimensions]))
        .filter(([n, dimensions]) => !isSameSize(n.data.__size, dimensions))
        .forEach(([n, dimensions]) => {
          dispatch.questionnaireModel.setSize({__uuid: n.data[0].__uuid, key: '__size', value: dimensions})
        });
    },
    pasteNodes: (position?: EventPosition, s?: RootState) => {
      const {copiedNodes: nodes, currentWorkflowId: workflowId} = s!.diagramModel;
      if (!nodes || nodes.length === 0 || !nodes[0]?.data) return;

      const offset = getOffsetFromNodes(nodes, position === 'pointer'
        ? s?.diagramModel?.currentMousePosition
        : position);
      const qs = nodes.map(n => n.data!)
      dispatch.questionnaireModel.duplicateQuestions({questions: qs, offset, workflowId});
    },
    duplicateNodes: (position?: EventPosition) => {
      dispatch.diagramModel.pasteNodes(position);
    },
    moveNodes: (position?: EventPosition, s?: RootState) => {
      const copiedNodes = selectedNodes(s!.diagramModel) || [];
      const offset = getOffsetFromNodes(copiedNodes, position === 'pointer'
        ? s?.diagramModel?.currentMousePosition
        : position);
      copiedNodes.forEach(node =>
        dispatch.questionnaireModel.updateQuestion({
          ...node.data!,
          __position: addPositions(node.position, offset),
          __workflowId: s?.diagramModel?.currentWorkflowId || node.data?.__workflowId
        })
      )
    },
  }),
  selectors: (slice, createSelector, hasProps) => ({
    selectedNodes() {
      return slice((diagram) => selectedNodes(diagram))
    },
    workflows() {
      return slice((diagram) => {
        const {questionNodes, hookNodes, questionEdges, hookEdges} = diagram;
        const nodes = [...hookNodes, ...questionNodes];
        const edges = [...hookEdges, ...questionEdges];
        return [
          {
            info: {
              __uuid: 'all',
              name: 'all workflows'
            },
            nodes: nodes,
            questions: questionNodes.map(n => n.data)
          } as Workflow,
          ...hookNodes.map<Workflow>((hn) => {
            const wns = getTreeFromNodes(hn, nodes, edges)
            const {keyPrefix, __uuid, Content} = hn.data[0];
            return ({
              info: {
                __uuid,
                name: Content.join(' / '),
                contentHook: Content,
                keyPrefix: keyPrefix || getKeyPrefix(hn.data[0].Content)[0].toLowerCase(),
              },
              nodes: wns,
              questions: wns.slice(1).map(n => n.data as QuestionUnion)
            });
          }).sort(sortWorkFlows)
          // ...questions.filter(q => q.__workflow)
          //   .map<Workflow>(q => ({
          //     info: q.__workflow!,
          //     questions: questions.filter(qi => qi.__workflowId === q.__workflow!.id)
          //   }))
        ]
      })
    },
    selectedEdges() {
      return slice((diagram) => selectedEdges(diagram))
    },
  })
})

const sortWorkFlows = (a: Workflow, b: Workflow) => b.info.name < a.info.name ? 1 : -1

export function getTreeFromNodes(n: Node, nodes: Node[], edges: Edge[]): Node[] {
  const ns = getOutgoers(n, nodes, edges)
  return _.uniq([n, ...ns, ...ns.flatMap(ni => getTreeFromNodes(ni, nodes, edges)).flat()])
}

const selectedNodes = (d: DiagramState) => d.questionNodes.filter(n => n.selected);
const selectedEdges = (d: DiagramState) => d.questionEdges.filter(e => e.selected);