import React, { useEffect, useMemo, useState } from 'react';
import './Diagram.scss';
import ReactFlow, { Background, Controls, MiniMap, Node, useReactFlow } from 'react-flow-renderer';
import { useDiagramController } from "./DiagramController";
import { useDiagram, useQuestionEditor } from "../hooks/useSelector";
import { useDiagramContextMenu } from "../Menus/DiagramContextMenus";
import { QuestionUnion } from "../../questionnaires/entities/question";
import { Workflow } from "../Worksheets/Workflows";
import { edgeTypes, nodeTypes } from "./Types";
import { useConnectionHandlers } from "../hooks/useConnectionHandlers";
import { useDispatchDiagram } from "../hooks/useDispatch";
import { useWorkflows } from "../hooks/useWorkflows";
import { color } from "../core/Colors";

const fitViewOptions = {duration: 200, padding: 0.5};

export function Diagram({value: {nodes}}: { value: Workflow }) {
  const {edges, contextMenuVisible} = useDiagram();
  const {
    onDoubleClickNode, onClickPane,
    onNodeDrag, onSelectionDragStop,
    onElementsRemove, onKeyDown,
    onNodesChange, onEdgesChange, onMouseMove
  } = useDiagramController();
  const {currentWorkflowId} = useWorkflows()
  const {after} = useQuestionEditor()
  const [contextMenu, setContextMenu] = useState<JSX.Element | null>(null);
  const {onNodeMenu, onSelectionMenu, onPaneMenu} = useDiagramContextMenu(setContextMenu);
  const {onConnectStart, onConnectStop, onConnect} = useConnectionHandlers(setContextMenu);
  const {init} = useDispatchDiagram();
  const {fitView} = useReactFlow()

  const ctMenuWrapper = useMemo(() => <div>
    {contextMenuVisible ? contextMenu : null}
  </div>, [contextMenuVisible, contextMenu]);

  useEffect(() => fitView(fitViewOptions), [currentWorkflowId, fitView]);

  return <div className="Diagram">
    <ReactFlow
      fitView
      // selectNodesOnDrag={true}
      nodesDraggable
      elementsSelectable
      nodesConnectable
      selectionKeyCode={'Shift'}
      deleteKeyCode={'Delete'}

      nodeTypes={nodeTypes}
      edgeTypes={edgeTypes}

      onInit={() => init()}

      nodes={nodes}
      edges={edges}
      onNodesChange={onNodesChange}
      onEdgesChange={onEdgesChange}

      minZoom={0.2}
      maxZoom={0.8}
      tabIndex={0}
      fitViewOptions={fitViewOptions}

      onConnectStart={onConnectStart}
      onConnectStop={onConnectStop}
      onConnect={onConnect}

      onEdgeContextMenu={(event, el) => {
        event.preventDefault()
      }}
      onSelectionContextMenu={onSelectionMenu}
      onSelectionDragStop={onSelectionDragStop}

      onNodesDelete={onElementsRemove}
      onNodeDoubleClick={onDoubleClickNode}
      // onNodeDragStart={onNodeDragStart}
      onNodeDragStop={onNodeDrag}

      onNodeContextMenu={onNodeMenu}

      onPaneContextMenu={onPaneMenu}
      onPaneClick={onClickPane}

      onMouseMove={onMouseMove}
      onKeyDown={onKeyDown}
    >
      <MiniMap
        maskColor='#44444499'
        style={{backgroundColor: '#22222299'}}
        nodeColor={(node: Node) =>
          node.data?.__uuid === after?.__uuid
            ? '#11ffff99'
            : '#22222299'}
        nodeStrokeWidth={40}
        nodeStrokeColor={(node: Node<QuestionUnion>) => node.data?.QuestionType ? color(node.data?.QuestionType) : '#cc9922'}
      />
      <Controls fitViewOptions={fitViewOptions}/>
      <Background/>
    </ReactFlow>
    {ctMenuWrapper}
  </div>
}
