import React, { PropsWithChildren } from "react";
import "./GenericObjectComponent.scss";
import { UniqueObject } from "../../questionnaires/entities/anyObject";
import { GenericObjectComponent } from "./GenericObjectComponent";
import { useGenericObjectListComponentEvents } from "./GenericObjectListComponentController";
import { ValueComparisonComponent } from "./ValueComparisonComponent";
import { ComparisonLogic, ComparisonValue } from "../../questionnaires/entities/comparison";
import ClickAwayListener from "react-click-away-listener";
import { LogicComparisonComponent } from "./LogicComparisonComponent";
import { Row } from "../core/Layout";
import { Btn } from "../core/Btn";
import { faPlus } from "@fortawesome/free-solid-svg-icons";
import { useDispatch } from "react-redux";
import { Dispatch } from "../../models";
import { faTrashAlt } from "@fortawesome/free-regular-svg-icons";


export type GenericObjectListComponentProps = {
  parent: UniqueObject
  propName: string,
  itemsNest: UniqueObject[] | UniqueObject[][],
  className?: string
  highlight?: boolean,
  onClick?: () => void,
}

export function GenericObjectListComponent(
  {
    highlight,
    className,
    onClick,
    ...props
  }: PropsWithChildren<GenericObjectListComponentProps>
) {
  const {
    propName, items, container, current, setCurrentId,
    canAddOrDelete, onAddItem, onDeleteItem, changeItemPropertyValue,
    resetCurrent
  } = useGenericObjectListComponentEvents(props);
  const {deleteProperty} = useDispatch<Dispatch>().questionEditorModel;
  if (!items) return null

  return (
    <ClickAwayListener onClickAway={() => resetCurrent()}>
      <div
        className={`GenericObjectListComponent ${className || ''} ${highlight ? 'highlight' : ''}`}
        onClick={onClick}
        onFocus={() => onClick && onClick()}
      >
        <div className="header">
          <span>{propName}</span>
          <span>
            <Btn
              icon={faTrashAlt}
              onClick={() => deleteProperty(propName)}
              tabIndex={0}
            />
          </span>
        </div>
        <LogicComparisonComponent
          logicComparison={container as ComparisonLogic}
          onChange={p => changeItemPropertyValue({...p, __uuid: container?.__uuid})}
        />
        <div style={{flex: 1}}>
          {items.map((item, i) => <div
            key={item.__uuid}
            className={`ItemContainer ${item === current ? 'highlight' : ''}`}
            onClick={() => setCurrentId(item.__uuid)}
            onFocus={() => setCurrentId(item.__uuid)}
          >
            <Row>
              {propName === 'Comparisons'
                ? <ValueComparisonComponent
                  valueComparison={item as ComparisonValue}
                  propName={propName}
                />
                : <GenericObjectComponent
                  propName={propName}
                  item={item}
                />
              }
              <Btn
                onClick={onDeleteItem}
                icon={faTrashAlt}
              >
              </Btn>
            </Row>
          </div>)}
        </div>
        <div className="header">
          <span></span>
          {canAddOrDelete &&
            <span>
          <Btn
            icon={faPlus}
            onClick={onAddItem}
            tabIndex={0}
          />
        </span>
          }
        </div>
      </div>
    </ClickAwayListener>
  );
}
