import { AnyObject } from "./anyObject";
import { Path } from "./selector";

export function getObjectProperties(root: AnyObject, keys: Path) {
  const child = keys.reduce<AnyObject | string | number | undefined>((o, key) => {
    return typeof o === 'object' ? o[key] : undefined;
  }, root)
  return child
    ? typeof child === 'object'
      ? Object.keys(child)
      : typeof child
    : undefined;
}

