import { ContentHook, HookType, TriggeredBy, triggeredByOptions } from "../../questionnaires/entities/triggeredBy";
import { FC, useCallback, useMemo } from "react";
import { DropDownMenu } from "../core/DropDownMenu";
import { MenuItem } from "../core/PopupMenu";
import { faAngleDown, faStepForward } from "@fortawesome/free-solid-svg-icons";
import './QuestionHookComponent.scss'
import { Property } from "../Details/PropertyEditor/Property";
import { Label } from "../core/Label";
import { useDispatchQuestionnaire } from "../hooks/useDispatch";
import { Handle, NodeProps, Position } from "react-flow-renderer";
import { Row } from "../core/Layout";
import { Btn } from "../core/Btn";
import { faTrashAlt } from "@fortawesome/free-regular-svg-icons";

export const QuestionHookComponent: FC<NodeProps<TriggeredBy[]>> = ({data, id}) => {
  const {removeHook} = useDispatchQuestionnaire();

  return <div className={`QuestionHook `}>
    <Row className='header'>
      <Label text={`Starting trigger${data.length > 1 ? 's' : ''}`}/>
      <Btn icon={faTrashAlt} onClick={() => removeHook(id.replace('hook_', ''))}/>
    </Row>
    {data.map((h: any) => <Hook key={h.__uuid} hook={h}/>)}
  </div>
}

const Hook: FC<{ hook: any }> = ({hook}) => {
  const {changeItemPropertyValue} = useDispatchQuestionnaire();

  const changeHook = useCallback((path: ContentHook) => changeItemPropertyValue({
    __uuid: hook.__uuid,
    key: 'Content',
    value: path
  }), [hook.__uuid, changeItemPropertyValue]);

  const changeHook3rdValue = useCallback((v: number) => changeItemPropertyValue({
    __uuid: hook.__uuid,
    key: 'Content',
    value: hook.Content.slice(0, 2).concat(v)
  }), [hook.__uuid, hook.Content, changeItemPropertyValue]);

  const thirdValueName = useMemo(() => {
    switch (hook.Content[1]) {
      case "BarcodeFound":
        return 'Trace type id (optional)';
      case "StartTask":
        return 'Task type id (optional)';
      case "FinishWithProblemState":
        return 'Problem state id (optional)';
      case "TraceTypeChanged":
        return 'Trace type id (optional)';
    }
  }, [hook]);

  const hookItems = useMemo(() => Object.entries(triggeredByOptions)
    .reduce<MenuItem[]>((menu, [k, v]) => {
      const sub = Object.entries(v).map<MenuItem>(([sk, sv]) => ({
        title: `${k.replace('Tasks', 'Ride')}.${sk}`,
        value: () => changeHook([k, sk as HookType]),
        icons: [faStepForward]
      }))
      return menu.concat(sub)
    }, []), [changeHook]);

  return (
    <div className="Hook">
      <DropDownMenu
        icon={faAngleDown}
        label={hook.Content.slice(0, 2).join('.')}
        menuItems={hookItems}
        menuTitle="Select starting point"
        align="left"
      />
      {!thirdValueName ? null :
        <Property
          name={thirdValueName}
          value={hook.Content[2]}
          type="number"
          onChange={({value}) => changeHook3rdValue(value as number)}/>
      }
      <Handle type={"source"} position={Position.Bottom} className="Handle HookSource"/>
    </div>
  )
}