import React, { useCallback, useEffect, useMemo } from 'react';
import './QuestionNodeComponent.scss';
import { QuestionUnion } from "../../questionnaires/entities/question";
import { useDiagram, useQuestionEditor, useQuestionnaire } from "../hooks/useSelector";
import { Handle, NodeProps, Position, useUpdateNodeInternals } from "react-flow-renderer";
import { usePropState } from "../hooks/usePropState";
import { AnyObject } from "../../questionnaires/entities/anyObject";
import { QuestionNodeDetails } from "./QuestionNodeDetails";
import { useQuestionKeys } from "../hooks/useQuestionKeys";
import { color } from "../core/Colors";
import { getQuestionHooks } from "../../models/questionnaireFunctions";

const customNodeStyles = {};

// handles need to have some stuff set in the style prop to actually have effect and do positioning of edges correctly
const handleStyleGeneral = {
  border: '2px solid #aaaaaa',
  borderRadius: '100%',
  backgroundColor: '#222222',
  padding: '14px',
}

const triggerTypes: AnyObject = {
  "Any": "1 IN",
  "All": "∞ IN",
  "Equals": '=',
  "Regex": "/rx/",
  "GT": '>',
  "LT": '<',
  "Always": '*',
  "Comparison": '?≈?',
  "PersistentButton": 'BUT'
}

export function QuestionNodeComponent({data, id: nodeId, selected}: NodeProps<QuestionUnion>) {
  const {after} = useQuestionEditor();
  const {questions} = useQuestionnaire();
  const {questionEdges: edges, selectedEdges} = useDiagram();
  const isEdited = data.__uuid === after?.__uuid;
  const q = isEdited ? after || data : data;
  const {Triggers} = q;
  const hooks = getQuestionHooks(q);
  const hasHook = hooks?.length;
  const hookName = hooks && hooks[0] ? hooks[0].keyPrefix : undefined;
  const [triggers] = usePropState(Triggers || []);
  const questionKeys = useQuestionKeys();
  const updateNodeInternals = useUpdateNodeInternals();

  const questionKey = useMemo(() => q.Key, [q]);
  const isInputSelected = selectedEdges.some(sel => sel.target === questionKey);
  const selectedTriggerIds = useMemo(() => selectedEdges.map(e => e.sourceHandle).concat('_').join(','), [selectedEdges]);
  const isSelectedTrigger = useCallback((id: string) => selectedTriggerIds.includes(`${id},`), [selectedTriggerIds]);

  const input = useMemo(() => {
    const color = hasHook
      ? 'orange'
      : edges.some(edge => edge.target === questionKey)
        ? (isInputSelected
          ? '#61dafb'
          : 'gray')
        : 'black';
    return <Handle
      type="target"
      position={Position.Top}
      style={{
        ...handleStyleGeneral,
        transform: 'translate(-50%, -50%)',
        top: 0,
        left: `50%`,
        backgroundColor: color,
      }}
      className="Input"
      id={questionKey}
      key={questionKey}
    />;
  }, [questionKey, isInputSelected, edges, hasHook])

  const outputs = useMemo(() => {
    const dist = 100 / (triggers.length + 1);
    return [...triggers]
      .sort((a, b) => {
        const aX = questions.filter(q => a.QuestionKeys?.includes(q.Key)).reduce<number>((x, q, i, arr) => x + ((q.__position?.x || 0) / arr.length), 0);
        const bX = questions.filter(q => b.QuestionKeys?.includes(q.Key)).reduce<number>((x, q, i, arr) => x + ((q.__position?.x || 0) / arr.length), 0);
        return aX - bX
      })
      .map((tr, i) => {
        const label = `${triggerTypes[tr.TriggerType!]} ${tr.Value === undefined ? '' : tr.Value}`.trim()
        const uuid = `${tr.__uuid}`
        const color = !tr.QuestionKeys || tr.QuestionKeys.length === 0
          ? 'black'
          : tr.QuestionKeys.filter(k => k === '').some(k => !questionKeys.includes(k))
            ? 'red'
            : isSelectedTrigger(tr.__uuid) ? '#61dafb' : 'gray'

        return <Handle
          type="source"
          position={Position.Bottom}
          className="Output"
          style={{
            ...handleStyleGeneral,
            transform: 'translate(-50%, 50%)',
            bottom: 0,
            left: `${(i + 1) * dist}%`,
            backgroundColor: color,
          }}
          id={uuid}
          key={uuid}
          children={<span className={tr.TriggerType}>{label}</span>}
        />;
      })
  }, [triggers, questionKeys, isSelectedTrigger, questions]);

  const style = useMemo(() => ({
    ...customNodeStyles,
    minWidth: 150 + outputs.length * 60,
    "--type-color": color(q.QuestionType),
  } as React.CSSProperties), [outputs.length, q.QuestionType]);

  useEffect(() => {
    updateNodeInternals(nodeId)
  }, [updateNodeInternals, nodeId, outputs])

  return (
    <div
      className={`QuestionNode${
        isEdited ? ' edited' :
          selected ? ' selected' : ''
      }`}
      style={{...style}}
    >
      {input}
      <div className="HookName">{hookName}</div>
      <QuestionNodeDetails q={data}/>
      {outputs.map(el => el)}
    </div>
  )
}
