import { MenuItem, PopupMenu } from "../core/PopupMenu";
import React, { CSSProperties, useCallback, useEffect, useMemo, useState } from "react";
import { faClipboard } from "@fortawesome/free-regular-svg-icons";
import { Position } from "../../questionnaires/entities/question";
import * as _ from "lodash";
import { useAddQuestionItems } from "../hooks/useAddQuestionItems";
import { useWorkflows } from "../hooks/useWorkflows";
import { useEditingItems } from "../hooks/useEditingItems";
import { QuestionTemplateFactory } from "../../questionnaires/factories/questionFactory";
import { useAddQuestion } from "../hooks/useAddQuestion";
import { useReactFlow } from "react-flow-renderer";

interface PaneContextMenuProps {
  position?: Position,
  style?: CSSProperties,
  onClose: () => void,
}

export function PaneContextMenu({onClose, style, position}: PaneContextMenuProps) {
  const [popup, setPopup] = useState<JSX.Element | null>(null);
  const questionTypeItems = useAddQuestionItems();
  const {currentWorkflowId} = useWorkflows()
  const {pasteMenuItems} = useEditingItems();
  const addQuestion = useAddQuestion();
  const {project} = useReactFlow();

  const popupItems = useMemo<MenuItem[] | undefined>(() => [
      {title: 'Add Question', value: 'Add', icons: [faClipboard], subItems: questionTypeItems} as MenuItem,
      ...pasteMenuItems
    ]
    , [questionTypeItems, pasteMenuItems]);

  const doAddQuestion = useCallback((f: QuestionTemplateFactory) => {
    addQuestion(
      f,
      position,
      currentWorkflowId,
    );
  }, [addQuestion, position, currentWorkflowId]);

  const onItemClick = useCallback((item: MenuItem) => {
    const rootValue = _.castArray(item.value)[0];
    console.log('context ', item.value, rootValue)
    if (rootValue === 'Add') {
      doAddQuestion(item.value[1] as QuestionTemplateFactory);
      return
    }
    if (typeof rootValue === 'function') {
      rootValue(position ? project(position) : undefined);
    }
  }, [doAddQuestion, position, project]);

  useEffect(() => {
    setPopup(<PopupMenu
      position={position}
      style={style}
      items={popupItems}
      onItemClick={onItemClick}
      onClose={onClose}
      align="float"
    />)
  }, [position, style, popupItems, onItemClick, onClose, setPopup]);

  return popup
}