import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { QuestionUnion, WhenToShowTypes } from "../../questionnaires/entities/question";
import { useMemo } from "react";
import { faEdit, faHandPaper } from "@fortawesome/free-regular-svg-icons";
import { faEllipsisH, faFileImport, faLevelDownAlt, faTrashAlt, } from "@fortawesome/free-solid-svg-icons";
import { SideEffectType } from "../../questionnaires/entities/sideEffect";
import { IconDefinition } from "@fortawesome/free-brands-svg-icons";
import { useQuestionSpecificComponent } from "./QuestionSpecificDetailsComponent";
import { IconName } from "@fortawesome/fontawesome-common-types";
import { Property } from "../Details/PropertyEditor/Property";
import { useQuestionHandlers } from "../Details/QuestionItemController";
import { useDispatchQuestionEditor, useDispatchQuestionnaire } from "../hooks/useDispatch";
import { KeyEditComponent } from "../Details/PropertyEditor/KeyEditComponent";
import { Btn } from "../core/Btn";

type QuestionNodeDetailsProps = {
  q: QuestionUnion;
}

function getSideEffectIcon(type: SideEffectType): IconDefinition {
  return type === 'Block' ? faHandPaper :
    type === 'Cancel' ? faTrashAlt :
      faFileImport
}

export function QuestionNodeDetails({q}: QuestionNodeDetailsProps) {
  const {SideEffect, WhenToShow, Icon} = q;
  const {setEditedQuestion} = useDispatchQuestionEditor();
  const {deleteQuestion} = useDispatchQuestionnaire();
  const {
    onChangeValue,
    onKeyPropChange
  } = useQuestionHandlers();

  const customIcon = useMemo(() => {
    if (!Icon) return null;
    return <FontAwesomeIcon icon={{
      prefix: 'fas',
      // IconName is defined in the code that will be generated at build time and bundled with this file.
      iconName: Icon as IconName,
    }}/>
  }, [Icon]);

  const sideEffects = useMemo(() => {
    const effects = (SideEffect && SideEffect.length) ? SideEffect : []
    return effects.map(eff =>
      <FontAwesomeIcon key={eff.__uuid} icon={getSideEffectIcon(eff.SideEffectType)}/>
    )
  }, [SideEffect]);

  const whenToShow = useMemo(() => {
    if (!WhenToShow) return null;
    const index = WhenToShowTypes.indexOf(WhenToShow);
    const left = [-4, 5, 20][index];
    return <div key="WhenToShow" style={{
      position: 'relative',
      display: 'flex',
      flexDirection: 'column',
      justifyContent: "flex-end",
      width: 30,
      height: 20
    }}>
      <FontAwesomeIcon key="1" icon={faLevelDownAlt} style={{
        position: 'absolute',
        left: 5 + left,
        bottom: -8,
        fontSize: '1.3em'
      }}/>
      <FontAwesomeIcon key="2" icon={faEllipsisH} style={{position: 'absolute', left: 5, bottom: -25}}/>
    </div>
  }, [WhenToShow]);

  const specific = useQuestionSpecificComponent(q)

  return <div className={`QuestionNodeDetails`} onFocus={() => setEditedQuestion(q)}>
    <div className="Icons">
      {sideEffects}
      {whenToShow}
      <Btn className="Edit" icon={faEdit} onClick={() => setEditedQuestion(q)}/>
      <Btn className="Delete" icon={faTrashAlt} onClick={() => deleteQuestion(q)}/>
    </div>
    <div className="Title">
      <KeyEditComponent
        value={q.Key}
        name="Key"
        onBlur={onKeyPropChange}
      />
      <Property
        value={q.Title}
        name="Title"
        type="string"
        onChange={onChangeValue}
      />
      <span>{customIcon}</span>
    </div>
    {specific}
  </div>
}