import React, { FC, useCallback, useMemo } from 'react';
import { EdgeProps, getEdgeCenter, getSmoothStepPath, XYPosition, } from 'react-flow-renderer';
import './ConnectionComponent.scss'
import { usePropState } from "../hooks/usePropState";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTimesCircle } from "@fortawesome/free-regular-svg-icons";
import { useDispatchQuestionnaire } from "../hooks/useDispatch";

const arrowSize = 12;

export const ConnectionComponent: FC<EdgeProps> = (
  {
    id,
    sourceX,
    sourceY,
    targetX,
    targetY,
    sourcePosition,
    targetPosition,
    style = {},
    selected,
  }) => {
  const {removeConnection} = useDispatchQuestionnaire()
  const edgePath = getSmoothStepPath({
    sourceX,
    sourceY,
    sourcePosition,
    targetX,
    targetY: targetY - arrowSize,
    targetPosition,
    borderRadius: 20,
  });
  const [x, y] = getEdgeCenter({
    sourceX,
    sourceY,
    targetX,
    targetY: targetY - arrowSize,
  });
  const targetKey = useMemo(() => id.substr(36), [id]);
  const triggerId = useMemo(() => id.substr(0, 36), [id]);

  const onButtonClick = useCallback((evt: React.MouseEvent<HTMLButtonElement> | undefined) => {
    if (selected) {
      removeConnection({targetKey, triggerId})
      evt?.stopPropagation();
    }
  }, [removeConnection, targetKey, triggerId, selected]);

  const [via] = usePropState<XYPosition>({x, y})

  return (
    <>
      <path
        id={id}
        style={style}
        className={`react-flow__edge-path`}
        d={edgePath}
      />
      <polygon
        className={'connection-arrowhead'}
        points={`${targetX} ${targetY} ${targetX + arrowSize} ${targetY - arrowSize} ${targetX - arrowSize} ${targetY - arrowSize}`}
        stroke="transparent" strokeWidth="0"/>
      <foreignObject
        x={via.x}
        y={via.y}
        className={`connection-foreignobject`}
        requiredExtensions="http://www.w3.org/1999/xhtml"
      >
        <button
          className="edgebutton"
          onClick={onButtonClick}
        >
          <FontAwesomeIcon icon={faTimesCircle}/>
        </button>
      </foreignObject>
    </>
  );
}