import React, { PropsWithChildren} from "react";
import "./GenericObjectComponent.scss";
import { UniqueObject } from "../../questionnaires/entities/anyObject";
import GivePropertyComponent from "./PropertyEditor";
import { useObjectTransformer } from "./GenericObjectComponentController";

export type GenericObjectComponentProps = {
  item: UniqueObject,
  propName?: string,
}

export function GenericObjectComponent(
  {
    item,
    propName = '',
  }: PropsWithChildren<GenericObjectComponentProps>
) {
  const {propertyList, onChange, getOptions} = useObjectTransformer({item, propName});

  return (
    <div
      className={`GenericObjectComponent`}
    >
      {propertyList.map(({key, value}) => {
        return <GivePropertyComponent
          key={key.substr(0,8)}
          name={key}
          value={value}
          onChange={onChange}
          options={getOptions((key))}
        />
      })}
    </div>
  );
}
