import { AnyObject, makeKeyValueList } from "../../questionnaires/entities/anyObject";
import { useBuilder } from "./useBuilder";
import { useMemo } from "react";

export function usePropertyList(item: AnyObject, propName: string) {
  const builder = useBuilder();
  const itemOrder = useMemo(() => builder.getPropertyItemOrder(propName), [builder, propName]);
  return  useMemo(() => {
      const props = makeKeyValueList(item)
        .filter(({key}) => !key.startsWith('__'))
        .sort(({key: a}, {key: b}) =>
          itemOrder.indexOf(a) - itemOrder.indexOf(b)
        );
      return props.filter(p => !p.key.startsWith('_'))
    }
    , [item, itemOrder]);
}