import { MenuItem, PopupMenu } from "../core/PopupMenu";
import React, { CSSProperties, useCallback, useMemo } from "react";
import { Node } from "react-flow-renderer";
import { faClipboard, faClone, faEdit, faTrashAlt } from "@fortawesome/free-regular-svg-icons";
import { faPlus, faSignInAlt, faSignOutAlt } from "@fortawesome/free-solid-svg-icons";
import { Position, QuestionUnion } from "../../questionnaires/entities/question";
import { useDispatchDiagram, useDispatchQuestionEditor, useDispatchQuestionnaire } from "../hooks/useDispatch";
import { getQuestionHooks } from "../../models/questionnaireFunctions";

interface QuestionContextMenuProps {
  nodes: Node<QuestionUnion>[]
  position?: Position,
  style?: CSSProperties,
  onClose: () => void,
}

export function QuestionContextMenu({onClose, style, position, nodes}: QuestionContextMenuProps) {
  const {deleteQuestionsByKeys, addTrigger, addHook, removeHook} = useDispatchQuestionnaire();
  const {setEditedQuestion} = useDispatchQuestionEditor()
  const {duplicateNodes} = useDispatchDiagram()
  const {copyNodes} = useDispatchDiagram();

  const isOneNode = useMemo(() => nodes.length === 1, [nodes.length]);
  const canAddHook = useMemo(() => isOneNode && !getQuestionHooks(nodes[0].data), [isOneNode, nodes]);
  const canRemoveHook = useMemo(() => isOneNode && !!getQuestionHooks(nodes[0].data), [isOneNode, nodes]);

  const popupItems = useMemo<MenuItem[] | undefined>(() => {
      return !nodes || nodes.length === 0 ? undefined : [
        ...(isOneNode ? [{
          title: 'Add trigger',
          value: 'AddHook',
          disabled: !canAddHook,
          icons: [faPlus, faSignInAlt]
        }] : []),
        ...(isOneNode ? [{
          title: 'Remove trigger',
          value: 'RemoveHook',
          disabled: !canRemoveHook,
          icons: [faTrashAlt, faSignInAlt]
        }] : []),
        ...(isOneNode ? [{title: 'Add output', value: 'AddOutput', icons: [faPlus, faSignOutAlt]}] : []),
        {title: 'Duplicate', value: 'Duplicate', icons: [faClone]},
        {title: 'Copy', value: 'Copy', icons: [faClipboard]},
        {title: 'Edit', value: 'Edit', icons: [faEdit]},
        {title: 'Remove', value: 'Remove', icons: [faTrashAlt]},
      ];
    }
    , [nodes, canAddHook, isOneNode, canRemoveHook]);

  const deleteQuestions = useCallback(() => {
    deleteQuestionsByKeys(nodes.map(n => n.id));
  }, [nodes, deleteQuestionsByKeys]);

  const addTriggers = useCallback(() => {
    nodes.forEach(n => addTrigger(n.data!.__uuid))
  }, [nodes, addTrigger]);

  const addHooks = useCallback(() => {
    nodes.forEach(n => addHook(n.data!.__uuid))
  }, [nodes, addHook]);

  const removeHooks = useCallback(() => {
    nodes.forEach(n => removeHook(n.data!.__uuid))
  }, [nodes, removeHook]);

  const doDuplicateNodes = useCallback(() => {
    duplicateNodes()
  }, [duplicateNodes]);

  const doCopyNodes = useCallback(() => {
    copyNodes(nodes)
  }, [copyNodes, nodes]);

  const editQuestion = useCallback(() => {
    setEditedQuestion(nodes[0]?.data)
  }, [setEditedQuestion, nodes]);

  const onItemClick = useCallback((item: MenuItem) => {
    switch (item.value) {
      case 'AddHook':
        addHooks()
        break;
      case 'RemoveHook':
        removeHooks()
        break;
      case 'AddOutput':
        addTriggers();
        break;
      case 'Duplicate':
        doDuplicateNodes();
        break;
      case 'Copy':
        doCopyNodes()
        break;
      case 'Edit':
        editQuestion()
        break;
      case 'Remove':
        deleteQuestions();
        break;
    }
  }, [addHooks, doDuplicateNodes, addTriggers, deleteQuestions, doCopyNodes, removeHooks, editQuestion]);

  return <PopupMenu
    position={position}
    style={style}
    menuTitle="Selection"
    items={popupItems}
    onItemClick={onItemClick}
    onClose={onClose}
    align="float"
  />
}