import { MenuAlign, MenuItem, PopupMenu } from "../core/PopupMenu";
import React, { useCallback } from "react";
import { Position, QuestionUnion } from "../../questionnaires/entities/question";
import { useAddQuestionItems } from "../hooks/useAddQuestionItems";
import { useWorkflows } from "../hooks/useWorkflows";
import { QuestionTemplateFactory } from "../../questionnaires/factories/questionFactory";
import { useAddQuestion } from "../hooks/useAddQuestion";

interface AddQuestionMenuProps {
  position?: Position,
  onClose: () => void,
  align: MenuAlign,
  callback?: (q: QuestionUnion) => void,
}

export function AddQuestionMenu({onClose, position, align, callback}: AddQuestionMenuProps) {
  const {currentWorkflowId} = useWorkflows()
  const addQuestion = useAddQuestion()
  const items = useAddQuestionItems();

  const onAddQuestion = useCallback((item: MenuItem) => {
    addQuestion(
      item.value as QuestionTemplateFactory,
      position,
      currentWorkflowId,
      callback
    );
  }, [addQuestion, position, currentWorkflowId, callback]);

  const onInnerClose = useCallback(() => {
    onClose();
  }, [onClose]);

  return <PopupMenu
    position={position}
    menuTitle="Add Question"
    items={items}
    onItemClick={onAddQuestion}
    onClose={onInnerClose}
    align={align}
  />
}