import { useDispatchDiagram, useDispatchQuestionnaire } from "./useDispatch";
import React, { MouseEvent, useCallback, useRef } from "react";
import { Connection, OnConnectStartParams, OnConnectStop } from "react-flow-renderer";
import { QuestionUnion } from "../../questionnaires/entities/question";
import { AddQuestionMenu } from "../Menus/AddQuestionMenu";
import { OnConnectStart } from "react-flow-renderer/dist/esm/types";
import * as _ from "lodash";
import { isExistingKey } from "../../models/questionnaireFunctions";
import { useDiagram } from "./useSelector";
import { useWorkflows } from "./useWorkflows";

export const useConnectionHandlers = (setContextMenu: React.Dispatch<React.SetStateAction<JSX.Element | null>>) => {
  const {currentWorkflow} = useWorkflows();
  const {questions} = currentWorkflow || {questions: []};

  const {hookNodes} = useDiagram();
  const {addConnection, changeHookParent} = useDispatchQuestionnaire();
  const {showContextMenu} = useDispatchDiagram();
  const refConnect = useRef<OnConnectStartParams | undefined>(undefined);

  const onClose = useCallback(() => {
    showContextMenu(false)
  }, [showContextMenu]);

  const addConnectionToQuestion = useCallback((q: QuestionUnion) => {
    console.log('connect new question ', refConnect.current)
    if (refConnect && refConnect.current?.handleId && refConnect.current.handleType === 'source')
      addConnection({triggerId: refConnect.current.handleId, targetKey: q.Key});
  }, [addConnection, refConnect]);

  const onAddAndConnectMenu = useCallback((event: MouseEvent) => {
    event.preventDefault();
    setContextMenu(<AddQuestionMenu
      align="float"
      key="ContextMenu"
      position={{x: event.clientX, y: event.clientY}}
      callback={addConnectionToQuestion}
      onClose={onClose}
    />)
    showContextMenu(true);
  }, [setContextMenu, onClose, showContextMenu, addConnectionToQuestion]);

  const onConnectStart = useCallback<OnConnectStart>((event, params) => {
    refConnect.current = _.cloneDeep(params)
    console.log('start connect ', refConnect.current)
  }, [refConnect]);

  const onConnectStop = useCallback<OnConnectStop>((event) => {
    // a hacky way to check if the event was actually a onConnection event too.
    if ((event.target as any).dataset?.handleid) return;
    if (refConnect.current?.handleType === 'target') return;
    console.log('stop connect ', refConnect.current)
    onAddAndConnectMenu(event as unknown as MouseEvent)
  }, [onAddAndConnectMenu]);


  const onConnect = useCallback((connection: Connection) => {
    console.log('onConnect')
    if (isExistingKey(questions, connection.source || '')) {
      if (!connection.sourceHandle || !connection.target) return;
      addConnection({
        triggerId: connection.sourceHandle,
        targetKey: connection.target,
      })
    } else {
      const hn = hookNodes.find(hn => hn.id === connection.source)
      if (hn && connection.target) {
        hn.data.forEach(tr =>
          changeHookParent({h: tr, qKey: connection.target!})
        )
      }
    }
  }, [addConnection, questions, changeHookParent, hookNodes]);

  return {
    onConnectStart, onConnectStop, onConnect
  }
}