import * as React from 'react';
import { ReactElement } from 'react';
import { DivProps } from "./CoreProp";

export interface MapProps<T> extends DivProps {
  items: T[] | undefined;
  itemRenderer: (item: T, index: number, array: T[]) => JSX.Element;
}

export function Map<T>({items, itemRenderer, ...props}: MapProps<T>): ReactElement | null {
  return <div {...props}>{items && items.length > 0 ? items.map(itemRenderer) : null}</div>;
}
