import { Trigger } from "./trigger";
import { Optional } from "../../core/typeHelpers";
import { TriggeredBy } from "./triggeredBy";
import { Selector } from "./selector";
import { OptionsSelector, QuestionOption } from "./options";
import { Comparison } from "./comparison";
import { SideEffect } from "./sideEffect";
import { RequestParam, RequestType } from "./request";
import { FunctionArgumentsType } from "./functionArguments";
import { UniqueObject } from "./anyObject";

export type QuestionKey = string;

export type OptionalQuestionProperties = Optional<QuestionBase>;

export const QuestionTypes = [
  "Select"
  , "Text"
  , "Alert"
  , "Timestamp"
  , 'TimestampElapsed'
  , 'TimestampAgeYears'
  , 'Picture'
  , "SingleDigit"
  , "ThreeDimensions"
  , "RoadlinkRequest"
  , "FunctionCall"
  , "AutoAnswer"
] as const;

export type QuestionType = typeof QuestionTypes[number];
export const InputTypes = ["number", "text", "barcode"] as const;
export type InputType = typeof InputTypes[number];
export const WhenToShowTypes = ["now", "next", "last"] as const;
export type WhenToShowType = typeof WhenToShowTypes[number];

export interface Position {
  x: number;
  y: number;
}

export interface Size {
  width: number;
  height: number;
}

type QuestionBase = UniqueObject & {
  Key: QuestionKey;
  QuestionType: QuestionType;
  Title: string;
  Icon?: string;
  Triggers?: Trigger[];
  TriggeredBy?: TriggeredBy[][],
  SideEffect?: SideEffect[],
  WhenToShow?: WhenToShowType,
  DontSend?: boolean,
  __position?: Position,
  __size?: Size,
  __workflowId?: string,
}

export type QuestionText = QuestionBase & {
  InputType?: InputType,
}
export type QuestionAlert = QuestionBase & {
  Message?: string,
  MessageSelector?: Selector,
}

export type QuestionSelect = QuestionBase & {
  Options?: QuestionOption[]
  OptionsSelector?: [OptionsSelector],
}
export type QuestionAutoAnswer = QuestionBase & {
  Comparisons?: Comparison[],
  AnswerValue?: number | string,
  AnswerSelector?: Selector,
}
export type QuestionFunctionCall = QuestionBase & {
  FunctionSelector?: string[],
  FunctionArguments?: FunctionArgumentsType[],
}
export type RoadlinkRequest = QuestionBase & {
  RequestType?: RequestType,
  RequestParams?: [RequestParam],
}
export type QuestionPicture = QuestionBase & {}
export type QuestionTimestamp = QuestionBase & {}
export type QuestionTimestampElapsed = QuestionBase & {}
export type QuestionTimestampAgeYears = QuestionBase & {}
export type QuestionSingleDigit = QuestionBase & {}
export type QuestionThreeDimensions = QuestionBase & {}

export type QuestionUnion = QuestionBase & QuestionSelect & QuestionText & QuestionAlert & QuestionAutoAnswer &
  QuestionFunctionCall & RoadlinkRequest & QuestionTimestamp & QuestionTimestampElapsed &
  QuestionSingleDigit & QuestionThreeDimensions & QuestionPicture & QuestionTimestampAgeYears;

