import React from 'react';
import './Menu.scss';
import { useDocument } from "../hooks/useSelector";
import { FileMenu } from "./FileMenu";
import { EditMenu } from "./EditMenu";
import { ToolBar } from "../core/ToolBar";
import { faPlus } from "@fortawesome/free-solid-svg-icons";
import { AddQuestionMenu } from "./AddQuestionMenu";
import { DropDownBtn } from "../core/DropDownBtn";
import { Label } from "../core/Label";
import pj from '../../../package.json'
import { Spacer } from "../core/Spacer";

const version = document.querySelector('meta[name="build-version"]')?.textContent ?? pj.version;

export function Menu() {
  const {fileName} = useDocument();
  return (
    <div className="Menu">
      <FileMenu/>
      <EditMenu/>
      <DropDownBtn
        label='Question'
        icon={faPlus}
        align="left"
        menu={AddQuestionMenu}
      />
      <ToolBar/>
      <Label text={fileName} className="filename"/>
      <Spacer/>
      <Label text={`v ${version}`} className="version"/>
    </div>
  )
}