export type FileOpenResult = {
  source: string | undefined,
  content: string,
}

function getDateTime() {
  return '-' + new Date().toISOString()
    .replaceAll(':', '')
    .replaceAll('-', '')
    .replaceAll('T', '-')
    .split('.')[0];
}

const datetimeRegex = /-\d\d\d\d[0-3]\d[01]\d-[0-2]\d[0-5]\d[0-6]\d/gi;

export const hasFileAccess = () => (!!window['showSaveFilePicker'] && !!window['showSaveFilePicker'])

export class FilePicker {
  private fileHandle: any;
  private writableStream: any;
  private fileName: string = '';

  private pickerOpts = {
    types: [
      {
        description: 'Questionnaire json',
        accept: {
          'application/json': ['.json']
        }
      },
    ],
    excludeAcceptAllOption: true,
    multiple: false
  };

  private canWrite = async () => new Promise<boolean>(resolve => {
    if (!this.writableStream) resolve(false);
    const writer = this.writableStream.getWriter()
    writer.ready
      .then(resolve)
      .catch(() => resolve(false))
  })

  private async openFileFromHandle(fileHandle: any, keepExistingData: boolean): Promise<string | undefined> {
    if (!fileHandle) return;
    this.fileHandle = fileHandle;
    const file = await this.fileHandle.getFile();
    this.fileName = file.name;
    this.writableStream = await this.fileHandle.createWritable({keepExistingData});
    // this.writableStream = undefined
    return file.text();
  }

  public async openFile(): Promise<FileOpenResult | undefined> {
    try {
      if (!window['showSaveFilePicker']) {
        console.error('window.showSaveFilePicker does not exist!')
        return
      }
      // let entries = await Neutralino.os.showOpenDialog('Save your diagram', {
      //   filters: [
      //     {name: 'Images', extensions: ['jpg', 'png']},
      //     {name: 'All files', extensions: ['*']}
      //   ]
      // });
      // console.log('You have selected:', entries);
      const [handle] = await window.showOpenFilePicker(this.pickerOpts);
      const content = await this.openFileFromHandle(handle, true);
      return (content !== undefined)
        ? {source: this.fileName, content}
        : undefined
    } catch (e) {
      console.error(e);
    }
  };

  public async saveFile(content: string, fileName?: string, addDateTime: boolean = false): Promise<string | undefined> {
    try {
      if (!window.showSaveFilePicker) {
        console.error('window.showSaveFilePicker does not exist!')
        return
      }
      let handle = this.fileHandle;
      if (!handle || addDateTime) {
        let suggestedName = (fileName || this.fileName || 'Questionnaire');
        suggestedName = !addDateTime ? suggestedName
          : suggestedName.replace('.json', '')
          .replace(datetimeRegex, '') + getDateTime() + '.json';
        handle = await window.showSaveFilePicker({...this.pickerOpts, suggestedName});
      }
      if (!handle) return;
      await this.openFileFromHandle(handle, false);
      await this.writeTo(content);
      await this.closeFile();
      return this.fileName;
    } catch (e) {
      console.error(e);
    }
  };

  public async saveCurrent(content: string) {
    if (!await this.writeTo(content)) {
      await this.saveFile(content);
    }
  }

  public async writeTo(content: string): Promise<boolean> {
    try {
      await this.writableStream.write({type: "truncate", size: 0});
      await this.writableStream.write(content);
    } catch (e) {
      console.error(e);
      return false;
    }
    return true;
  }

  public async closeFile() {
    await this.writableStream.close();
    this.writableStream = undefined;
    // this.fileHandle = undefined;
    // this.fileName = '';
  };
}

export default new FilePicker();