import { createUniqueObject, UniqueObject } from "./anyObject";
import { PathMakerOptions } from "./selector";
import { getObjectProperties } from "./getObjectProperties";

// the Equals type is not implemented anywhere in code yet. It is in the Questionnaire controller, but no one calls the trigger.
// export const TriggeredByTypes = ["Hook", "Equals"] as const;
export const TriggeredByTypes = ["Hook"] as const;
export type TriggeredByType = typeof TriggeredByTypes[number];

export const ContentTypes = ["Tasks", "Task", "Good", "DepotScanning"] as const;
export type ContentType = typeof ContentTypes[number] | string;
export const TasksHookTypes = ["StartRide", "EndRide"] as const;
export const TaskHookTypes = ["StartTask", "FinishTask", "FinishWithProblemState", "FinishCashOnDelivery"] as const;
export const GoodHookTypes = ["TraceTypeChanged"] as const;
export const DepotScanningHookTypes = ["BarcodeFound"] as const;
export const HookTypes = [...TasksHookTypes, ...TaskHookTypes, ...GoodHookTypes, ...DepotScanningHookTypes]
export type HookType = typeof HookTypes[number];
export type ContentHook = [ContentType?, HookType?, number?];

export type TriggeredBy = UniqueObject & {
  TriggeredByType: TriggeredByType,
  Content: ContentHook,
  keyPrefix?: string,
}
export type TriggeredByKey = keyof TriggeredBy;

export const createTriggeredBy = (overrides?: Partial<TriggeredBy>): [TriggeredBy] => [createUniqueObject({
  TriggeredByType: "Hook",
  Content: [],
  ...overrides
})]

//hooks, no checking for driver app or crossdock app yet`
export const triggeredByAsPrefix = {
  "DepotScanning": {
    "BarcodeFound": ["scanned", "barcode_found", "found", "trace"],
  },
  "Tasks": {
    "StartRide": ["start_ride"],
    "EndRide": ["end_ride", "ride_done"],
  },
  "Task": {
    "StartTask": ["start_task", "arrival", "task", "task", 'pickup'],
    "FinishTask": ["finish_task", "done", "departure", "delivered", "completed"],
    "FinishWithProblemState": ["task_problem", "report_issue", "abort_task"],
    "FinishCashOnDelivery": ["cod", "cash_on_delivery"],
  },
  "Good": {
    "TraceTypeChanged": ["trace", "scanned", "trace_change", "trace_ok", "trace_problem"]
  }
}

export const getKeyPrefix = (contentHook: ContentHook) => {
  const [k1 = '', k2 = '', k3] = contentHook;
  const o1: any = triggeredByAsPrefix[k1 as typeof ContentTypes[number]];
  let prefixes: string[] = o1 ? o1[k2] : '';
  if (k3)
    prefixes = [...prefixes.map(p => `${p}_${k3}`), ...prefixes]
  return prefixes || [''];
}

export const triggeredByOptions = {
  "DepotScanning": {
    "BarcodeFound": 1,
  },
  "Tasks": {
    "StartRide": undefined,
    "EndRide": undefined,
  },
  "Task": {
    "StartTask": 1, // number value will give give type number in editor as last path value
    "FinishTask": undefined, // undefined means no further path items
    "FinishWithProblemState": 1, // number value will give give type number in editor as last path value
    "FinishCashOnDelivery": undefined,
  },
  "Good": {
    "TraceTypeChanged": 1
  }
}

export const contentOptionsBuilder: PathMakerOptions = (propName, path) => getObjectProperties(triggeredByOptions, path);