import { Reducer } from "@rematch/core";

const historyDepth = 50;
const positive = (x: number) => Math.max(0, x);

export interface StateWithHistory<S> {
  past: S[];
  present: S;
  future: S[];
}

export const createStateWithHistory = <S>(state: S): StateWithHistory<S> => ({
  past: [],
  present: state,
  future: [],
});

export const reduceToHistory = <S>({past, present, future}: StateWithHistory<S>, newPresent: S) => {
  if (!newPresent || present === newPresent) {
    return {past, present, future}
  }
  return {
    past: [...past.slice(positive(past.length - historyDepth)), present],
    present: newPresent,
    future: []
  }
};

export const createReducerWithPast = <S>(reducer: Reducer<S>): Reducer<StateWithHistory<S>> =>
  ({past, present, future}: StateWithHistory<S>, payload: any): StateWithHistory<S> => {
    const newPresent = reducer(present, payload)
    if (!newPresent || present === newPresent) {
      return {past, present, future}
    }
    return {
      past: [...past, present],
      present: newPresent,
      future: []
    }
  };

export const undoRedoReducers = <S>() => ({
  undo: ({past, present, future}: StateWithHistory<S>) => {
    if (!past.length) return {past, present, future};
    const previous = past[past.length - 1]
    const newPast = past.slice(0, past.length - 1)
    return {
      past: newPast,
      present: previous,
      future: [present, ...future]
    }
  },
  redo: ({past, present, future}: StateWithHistory<S>) => {
    if (!future.length) return {past, present, future};
    const next = future[0]
    const newFuture = future.slice(1)
    return {
      past: [...past, present],
      present: next,
      future: newFuture
    }
  },
});

export const createReducersWithHistory = <S>(reducers: { [key: string]: Reducer<S> }): { [key: string]: Reducer<StateWithHistory<S>> } => {
  const r = Object
    .entries(reducers || {})
    .reduce<{ [key: string]: Reducer<StateWithHistory<S>> }>(
      (acc, [n, red]) => ({
        ...acc,
        [n]: createReducerWithPast(red)
      }), {});
  console.log(r)
  return r as { [key: string]: Reducer<StateWithHistory<S>> };
};
