import React from 'react';
import './Details.scss';
import { QuestionItem } from './QuestionItem';
import { useQuestionEditor } from "../hooks/useSelector";
import { DivProps } from "../core/CoreProp";
import { WorkflowDetails } from "./WorkflowDetails";

export function Details(props: DivProps) {
  const {after: question} = useQuestionEditor();

  return (
    <div className="Details" {...props}>
      {question
        ? <QuestionItem/>
        : <WorkflowDetails/>
      }
    </div>
  )
}

