import { DivProps } from "./CoreProp";
import React from "react";

type Style = { style: React.CSSProperties }
export const Static: Style = {style: {flex: 0}};
export const Flexible: Style = {style: {flex: 1}};
export const Stretch: Style = {style: {flex: 1, height: '100%', width: '100%'}};

export interface RowProps extends DivProps {
  className?: string;
}

export const Row = ({className, style, ...props}: RowProps) => <div
  className={`Row ${className}`}
  style={{
    display: 'flex',
    flexDirection: 'row',
    width: '100%',
    ...style
  }}
  {...props}
/>

export const Column = ({className, style, ...props}: RowProps) => <div
  className={`Column ${className}`}
  style={{
    display: 'flex',
    flexDirection: 'column',
    height: '100%',
    ...style
  }}
  {...props}
/>