import React, { CSSProperties, MouseEvent, useCallback, useMemo, useRef, useState } from 'react';
import './PopupMenu.scss';
import { IconProp } from "@fortawesome/fontawesome-svg-core";
import { Position } from "../../questionnaires/entities/question";
import ClickAwayListener from "react-click-away-listener";
import { If } from "./If";
import { Map } from "./Map";
import { Btn } from "./Btn";
import { Label } from "./Label";

function givePopupMenuPosition({x, y}: Position): { left: string, top: string } {
  return {left: `${x}px`, top: `${y}px`}
}

export type MenuItem = {
  title?: string,
  value: any,
  disabled?: boolean
  icons?: IconProp[],
  subItems?: MenuItem[],
  toolTip?: string,
  hotkeys?: string,
}

export type MenuAlign = 'left' | 'right' | 'float';

export interface PopupMenuProps {
  menuTitle?: string,
  items?: MenuItem[],
  indexState?: [number, React.Dispatch<React.SetStateAction<number>>],
  position?: Position,
  style?: CSSProperties,
  onItemClick?: (item: MenuItem, event?: MouseEvent) => void,
  onClose: () => void,
  align?: MenuAlign,
}

/**
 * Opens when items is not undefined or an empty array. updates when items changes, cancels and closes
 * when clicked outside the menu, closed when an item is selected.
 * @param menuTitle showed at top of menu,
 * @param items setting this will make the menu render, else null is returned
 * @param indexState a state hook that can be used to change the selected index without a mouse or touch event
 * @param onItemClick called when item is clicked on, returning menu item and event
 *  @param style sets the main styles of the menu. Handy to position the menu wherever you want. using Left, Right, Top, Right
 * @param position
 * @param onClose mandatory. Is called whenever the menu closes by it's own account, so when cancelled or an item is clicked. make sure to reset or unset callers state with this handler
 * @param align wether the popup menu aligns left or right in respect to the origin point, or float as a context menu
 */
export function PopupMenu(
  {
    menuTitle,
    items,
    indexState,
    onItemClick,
    style,
    position,
    onClose,
    align = 'float'
  }: PopupMenuProps) {
  const ref = useRef<any>();
  let [index, setIndex] = indexState || [-1, () => {}];
  const posStyle = useMemo<CSSProperties>(() => position && givePopupMenuPosition(position) || {}, [position]);
  const [subMenu, setSubMenu] = useState<MenuItem | undefined>(undefined);
  const alignStyle = useMemo(() => align === 'right' ? {right: 0} : align === 'left' ? {left: 0} : {}, [align]);
  const onInnerClose = useCallback(() => {
    onClose()
  }, [onClose]);

  const onInnerItemClick = useCallback((item: MenuItem, event?: MouseEvent) => {
    if (item.subItems) {
      setSubMenu(item)
      return
    }

    onItemClick && onItemClick(item, event)
    onInnerClose();
  }, [onItemClick, onInnerClose]);

  const onInnerSubItemClick = useCallback((subMenu: MenuItem, subItem: MenuItem, event?: MouseEvent) => {
    onItemClick && onItemClick({
      ...subItem,
      value: [subMenu.value, subItem.value]
    }, event);
    onInnerClose();
  }, [onItemClick, onInnerClose]);

  const onMouseOver = useCallback((index: number, event: MouseEvent) => {
    setIndex(index);
  }, [setIndex]);

  const onRightMouseDown = useCallback((event: MouseEvent) => {
    onInnerClose();
    event.preventDefault();
  }, [onInnerClose]);

  const itemRenderer = useCallback((menuItem: MenuItem, i) => {
    const {title, value, icons, subItems, disabled, hotkeys, toolTip} = menuItem
    return <div key={i} className={value === 'separator' ? "Separator" : "MenuItem"}>
      {value === "separator" ? null : <>
        <Btn
          key={value}
          highlight={index === i}
          onMouseDown={event => value && onInnerItemClick(menuItem, event)}
          onMouseOver={event => value && onMouseOver(i, event)}
          label={title ? title : ['string', 'number'].includes(typeof value) ? value : JSON.stringify(value)}
          disabled={disabled}
          icon={icons}
          toolTip={toolTip}
        >
          <Label text={hotkeys} className='Hotkey'/>
        </Btn>
        <If condition={subMenu && subItems === subMenu.subItems}>
          <PopupMenu
            style={{...style}}
            items={subItems}
            onItemClick={(subItem, event) => value && onInnerSubItemClick(subMenu!, subItem, event)}
            onClose={() => {}}
          />
        </If>
      </>}
    </div>
  }, [onInnerItemClick, onInnerSubItemClick, onMouseOver, index, style, subMenu]);

  return (
    <If condition={items && items.length}>
      <ClickAwayListener onClickAway={() => onInnerClose()}>
        <div
          className={`PopupMenu ${align || ''}`}
          style={{...style, ...posStyle, ...alignStyle}}
          ref={ref}
          onContextMenu={onRightMouseDown}
        >
          <div className="MenuTitle"><Label text={menuTitle}/></div>
          <Map items={items} itemRenderer={itemRenderer}/>
        </div>
      </ClickAwayListener>
    </If>
  )
}
