import { useEffect} from "react";
import { PropertyBuilder, UiBuilder } from "../../questionnaires/factories/propertyBuilder";
import { store } from "../../store/store";
import { useSelector } from "react-redux";
import { RootState } from "../../models";

const questionKeysSelector = store.select.questionnaireModel.questionKeys;

export function useBuilder() {
  const builder = PropertyBuilder as UiBuilder;
  const questionKeys = useSelector((state: RootState) => questionKeysSelector(state))
  useEffect(() => builder.setGetPropertyValueOptions('QuestionKeys', () => questionKeys)
    , [questionKeys, builder]);
  return builder;
}