import { v4 } from "uuid";
import { Position, QuestionUnion, Size } from "./question";
import * as _ from "lodash";

export type UniqueObject = AnyObject & {
  __uuid: string,
  __position?: Position,
  __size?: Size,
}
export type AnyObject = {
  [key: string]: any,
}
export type Dictionary<T> = {
  [key: string]: T,
}

export type KeyValue<T> = { key: string, value: T, __uuid?: string }
export type PropertyKeyValue = KeyValue<AnyObject | string | number | string[] | AnyObject[] | AnyObject[][]>;

export function isNested(itemsNest: UniqueObject[] | UniqueObject[][]): itemsNest is UniqueObject[][] {
  return itemsNest.length > 0 && _.isArray(itemsNest[0])
}

export function createUniqueObject<T extends AnyObject>(template?: Partial<T>) {
  return {
    ...template || {},
    __uuid: v4(),
  } as unknown as T & UniqueObject;
}

export function findUniqueChild(thing: AnyObject | AnyObject[], uuid: string): AnyObject | undefined {
  if (_.isArray(thing)) {
    return thing.reduce<AnyObject | undefined>(
      (found, o) => found || findUniqueChild(o, uuid),
      undefined);
  } else if (thing === null || thing === undefined) {
    return undefined
  } else if (typeof thing === 'object') {
    if (thing && thing.__uuid === uuid) return thing;
    return makeKeyValueList(thing)
      .reduce<AnyObject | undefined>(
        (found, {value}) => found || findUniqueChild(value, uuid),
        undefined);
  }
}

export type UniqueChildContext = {
  child?: AnyObject,
  indexes?: number[],
  key?: string,
  parent?: AnyObject,
}

export function findUniqueChildAndContext(thing: AnyObject | AnyObject[], uuid: string): UniqueChildContext {
  if (_.isArray(thing)) {
    return thing.reduce<UniqueChildContext>(
      (pCon, o, index) => {
        if (pCon.child) return pCon;
        const con = findUniqueChildAndContext(o, uuid);
        if (con.child && (!con.parent)) {
          con.parent = thing;
          con.indexes = con.indexes ? [index, ...con.indexes] : [index];
          // con.key = pCon.key; // setting the key, will influence the mutateUniqueChildOfQuestion output. A bit ambiguous, but yeah.
        }
        return con;
      }, {});
  } else if (thing === null || thing === undefined) {
    return {}
  } else if (typeof thing === 'object') {
    if (thing.__uuid === uuid) return {child: thing};

    return makeKeyValueList(thing)
      .reduce<UniqueChildContext>(
        (con, o) => {
          if (con.child) return con;
          con = findUniqueChildAndContext(o.value, uuid);
          if (con.child && !con.parent) con = {
            ...con,
            key: o.key,
            parent: thing
          }
          return con;
        }, {});
  }
  return {};
}

export function mutateUniqueChildOfQuestion<Q extends QuestionUnion | QuestionUnion[]>(oneOrMore: Q, mutation: AnyObject): QuestionUnion | undefined {
  if (!oneOrMore) return;
  let mutatedQ: QuestionUnion | undefined = undefined;
  const qs = _.castArray(oneOrMore);
  qs.find(q => {
    mutatedQ = _.cloneDeep(q);
    let {child, indexes, parent, key} = findUniqueChildAndContext(mutatedQ, mutation.__uuid!);
    if (!child || !parent) return false;
    if (indexes !== undefined)
      parent[indexes[0]] = mutation;
    else if (key)
      // I think immer can't do this yet.
      // might need workaround if we have a nested object structure
      parent[key] = mutation;

    return true;
  })

  console.log('mutate clone', mutatedQ)
  return mutatedQ
}

export function makeKeyValueList(o: AnyObject): KeyValue<any>[] {
  return Object.keys(o)
    .filter(k => !k.startsWith('__'))
    .map((k) => {
      return {key: k, value: o[k]};
    });
}
