import { PropsWithChildren} from "react";
import "./GenericObjectComponent.scss";
import { UniqueObject } from "../../questionnaires/entities/anyObject";
import { GenericObjectComponent } from "./GenericObjectComponent";

export type PropertyObjectProps = {
  propName: string,
  item: UniqueObject,
  highlight?: boolean,
  onClick?: () => void,
}

export function PropertyObject(
  {
    highlight,
    propName,
    item,
    onClick,
  }: PropsWithChildren<PropertyObjectProps>
) {
  
  return (
    <div
      className={`PropertyObject ${highlight ? 'highlight' : ''}`}
      onClick={onClick}
      onFocus={() => onClick && onClick()}
    >
      <div className="header">
        <span>{propName}</span>
      </div>
      <GenericObjectComponent item={item} />
    </div>
  );
}
