import { createModel } from "@rematch/core";
import { RootModel } from "./index";
import { RootState } from "../models";
import filePicker, { FileOpenResult, hasFileAccess } from "../inOut/filePicker";
import { copyToClipboard, pasteFromClipboard } from "../inOut/clipboard";
import { QuestionUnion } from "../questionnaires/entities/question";
import { convertJsonDataToQuestionnaire } from "./questionnaireModel";
import * as _ from "lodash";

export interface DocumentState {
  hasFileAccess?: boolean,
  hasClipboardQuestionnaire?: boolean,
  fileName?: string,
  content?: string,
  clipboardIsValid?: boolean,
}

const initialState: DocumentState = {
  hasFileAccess: hasFileAccess(),
  hasClipboardQuestionnaire: false,
  fileName: undefined,
  content: '',
  clipboardIsValid: false
}
const giveQuestionnaireContent = (questions: QuestionUnion[], strip = false) => JSON.stringify(
  questions,
  strip ? ((k, v) => /^__/.test(k) ? undefined : v) : undefined, 2
);

function getQuestionnaire(jsonString: string): QuestionUnion[] | undefined {
  try {
    const json = JSON.parse(jsonString);
    return _.isArray(json) && json.length > 0
      ? json : undefined
  } catch (e) {
    // console.error(e, jsonString)
  }
}

export const documentModel = createModel<RootModel>()({
  state: initialState,
  reducers: {
    set: (state, payload: DocumentState) => {
      return {
        ...state,
        ...payload
      }
    },
    reset: () => {
      return initialState;
    },
    setNewFile: (state, {source, content}: FileOpenResult) => {
      return {
        ...state,
        fileName: source,
        content,
      }
    },
  },
  effects: (dispatch) => {
    return ({
      openFile: async () => {
        const result = await filePicker.openFile();
        if (!result) return;
        dispatch.documentModel.setAndCheckContent(result);
      },
      saveCurrent: async (stripped?: boolean, state?: RootState) => {
        const content = giveQuestionnaireContent(state?.questionnaireModel?.present.questions || [], stripped);
        const source = await filePicker.saveFile(content, state!.documentModel?.fileName);
        if (source)
          dispatch.documentModel.setNewFile({source, content});
      },
      saveToNew: async (stripped?: boolean, state?: RootState) => {
        const content = giveQuestionnaireContent(state?.questionnaireModel?.present.questions || [], stripped);
        const source = await filePicker.saveFile(content, state!.documentModel?.fileName, true);
        if (source)
          dispatch.documentModel.setNewFile({source, content});
      },
      checkClipboardContent: async (content?: any): Promise<boolean> => {
        content = content || await pasteFromClipboard();
        const json = getQuestionnaire(content);
        if (json) {
          try {
            const q = convertJsonDataToQuestionnaire(json);
            dispatch.documentModel.set({clipboardIsValid: !!q.length})
            return !!q.length
          } catch (e) {
          }
        }
        dispatch.documentModel.set({clipboardIsValid: false})
        return false
      },
      setAndCheckContent: async ({content, source}: FileOpenResult) => {
        try {
          const json = getQuestionnaire(content);
          if (!json) return;
          dispatch.questionnaireModel.replaceQuestions(json);
          dispatch.documentModel.setNewFile({content, source});
        } catch (e) {
          console.error(e, source, content)
        }
      },
      pasteFromClipboard: async () => {
        const content = await pasteFromClipboard()
        dispatch.documentModel.setAndCheckContent({content, source: undefined})
      },
      copyToClipboard: async (stripped?: boolean, state?: RootState) => {
        const content = giveQuestionnaireContent(state?.questionnaireModel?.present.questions || [], stripped);
        return copyToClipboard(content);
      },
    });
  }
});