import { GenericObjectListComponentProps } from "./GenericObjectListComponent";
import { useCallback, useMemo, useState } from "react";
import { createUniqueObject, isNested, UniqueObject } from "../../questionnaires/entities/anyObject";
import { useDispatch } from "react-redux";
import { Dispatch } from "../../models";
import { useBuilder } from "../hooks/useBuilder";
import * as _ from "lodash";
import { ComparisonLogic } from "../../questionnaires/entities/comparison";

export function useGenericObjectListComponentEvents({itemsNest, propName, parent}: GenericObjectListComponentProps) {
  const [currentId, setCurrentId] = useState<string>();
  const builder = useBuilder();
  const itemFactory = useMemo(() => builder.propertyItemFactory(propName), [propName, builder]);
  const {addItem, deleteItem, changeItemPropertyValue} = useDispatch<Dispatch>().questionEditorModel
  const [items, setItems] = useState<UniqueObject[]>([]);
  const [container, setContainer] = useState<UniqueObject | undefined>(undefined);

  const logicComparison = propName === 'Comparisons' ? itemsNest[0] as ComparisonLogic : undefined
  const nested =
    logicComparison
      ? logicComparison.Comparisons
      : isNested(itemsNest) ? itemsNest.flat(1) : itemsNest;

  if (!_.isEqual(nested, items)) {
    setItems(nested);
    setContainer(logicComparison);
  }

  const current = useMemo(() => {
    return items && items.find(item => item.__uuid === currentId)
  }, [items, currentId]);

  const onAddItem = useCallback(() => {
    if (!itemFactory) return;
    const newItem = current
      ? createUniqueObject(current)
      : items.length
        ? createUniqueObject(items.slice(-1).pop())
        : itemFactory();
    if (!newItem) return;
    setCurrentId(newItem.__uuid);
    addItem({parent: {__uuid: container?.__uuid || parent.__uuid, propKey: propName}, child: newItem});
  }, [container?.__uuid, current, items, propName, itemFactory, addItem, parent.__uuid]);

  const onDeleteItem = useCallback(() => {
    if (!currentId) return;
    const item = builder.propertyItemFactory(propName);
    if (item === undefined) return; // if factory returns undefined deleting is not allowed
    const index = items.findIndex(item => item.__uuid === currentId);
    deleteItem(currentId);
    setCurrentId(items[Math.max(index - 1, 0)].__uuid);
  }, [currentId, items, propName, setCurrentId, deleteItem, builder]);

  return {
    propName,
    items, container,
    current, setCurrentId,
    canAddOrDelete: !!itemFactory,
    onAddItem,
    onDeleteItem,
    changeItemPropertyValue,
    resetCurrent: () => setCurrentId(undefined)
  }
}