export type Path = (string | number)[]
export type PlainType = ReturnType<typeof getObjectProperties>

// returning empty array means any value is allowed. so you will have no limits in input but also no help.
// if the return array contains values, only those values can be inputted and they can be selected from a dropdown or
// will be auto completed when typing
export type PathMakerOptions = (propName: string, path: Path) => string[] | PlainType | undefined;

export const selectorOptionsBuilder: PathMakerOptions = (propName, path) => {
  if (['redux', 'reduxState', 'answer'].includes(`${path[0]}`)) return [];
  return getObjectProperties(objects, path);
}
