import { useDispatchDocument, useDispatchQuestionnaire } from "./useDispatch";
import { useDocument } from "./useSelector";
import { useMemo } from "react";
import { MenuItem } from "../core/PopupMenu";
import { faFileExport, faFileImport, faPaste } from "@fortawesome/free-solid-svg-icons";
import { faCopy, faTrashAlt } from "@fortawesome/free-regular-svg-icons";
import { useOsHotkeyCombos } from "./useOsSpecialKeys";

export function useFileItems() {
  const {openFile, saveToNew, saveCurrent, copyToClipboard, pasteFromClipboard} = useDispatchDocument();
  const {reset} = useDispatchQuestionnaire();
  const {hasFileAccess} = useDocument();
  const {cmd, alt, shift} = useOsHotkeyCombos();

  return useMemo<MenuItem[]>(() => [
    {
      title: 'Open...',
      icons: [faFileImport],
      value: () => {openFile()},
      disabled: !hasFileAccess,
      hotkeys: `${cmd}+o`,
      toolTip: 'Open questionnaire from file'
    },
    {
      title: 'Save...', icons: [faFileExport], value: () => {saveCurrent()}, disabled: !hasFileAccess,
      hotkeys: `${cmd}+s`,
      toolTip: 'Save questionnaire to file'
    },
    {
      title: 'Save to...', icons: [faFileExport], value: () => {saveToNew()}, disabled: !hasFileAccess,
      hotkeys: `${cmd}+${shift}+s`,
      toolTip: 'Save questionnaire to a new file'
    },
    {
      title: 'Save stripped to...', icons: [faFileExport], value: () => {saveToNew(true)}, disabled: !hasFileAccess,
      hotkeys: `${cmd}+${alt}+s`,
      toolTip: 'Save questionnaire to a new file'
    },
    {value: 'separator'},
    {
      title: 'Paste Questionnaire JSON',
      icons: [faPaste],
      value: () => {pasteFromClipboard()},
      // disabled: !clipboardIsValid,
      toolTip: 'Import a complete questionnaire json from the clipboard'
    },
    {
      title: 'Copy Q. JSON',
      icons: [faCopy],
      value: () => {copyToClipboard()},
      hotkeys: `${cmd}+${shift}+c`,
      toolTip: 'Copy the complete questionnaire json to the clipboard'
    },
    {
      title: 'Copy Stripped Q. JSON',
      icons: [faCopy],
      value: () => {copyToClipboard(true)},
      hotkeys: `${cmd}+${alt}+c`,
      toolTip: 'Copy the complete questionnaire json, stripped from meta data for the editor to the clipboard'
    },
    {
      title: 'Clear All', icons: [faTrashAlt], value: () => {reset()},
      toolTip: 'Wipe the complete questionnaire'
    },

  ], [openFile, saveToNew, saveCurrent, copyToClipboard, pasteFromClipboard, reset, hasFileAccess, cmd, alt, shift]);

}