import './Property.scss';
import React, { useCallback, useEffect, useMemo, useRef, useState } from "react";
import { MenuItem, PopupMenu } from "../../core/PopupMenu";
import { KeyValue } from "../../../questionnaires/entities/anyObject";
import { usePropState } from "../../hooks/usePropState";
import { IconName } from "@fortawesome/fontawesome-common-types";
import * as _ from "lodash";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faAngleDown } from "@fortawesome/free-solid-svg-icons";

type PropertySelectListProps = {
  name: string,
  value: string | number,
  options: (string)[] | undefined,
  type?: 'string' | 'number' | 'icon',
  label?: string | JSX.Element,
  allowCustomInput?: boolean,
  onChange?: (p: KeyValue<string>) => void,
  onEditValue?: (p: KeyValue<string>) => void,
  onFocus?: (event?: { currentTarget: EventTarget & HTMLElement }) => void,
}

export const PropertySelectList: React.FunctionComponent<PropertySelectListProps> = (
  {
    name,
    value,
    options,
    label,
    allowCustomInput = false,
    onChange,
    onEditValue,
    onFocus,
    type = "string",
  }) => {
  const [input, setInput] = usePropState(`${value}`);
  const [oriList] = usePropState(options || []);
  const [filteredList, setFilteredList] = usePropState(options || []);
  const resetFilteredList = useCallback(() => setFilteredList(oriList), [oriList, setFilteredList])
  const [index, setIndex] = useState(-1);
  const [showPopup, setShowPopup] = useState(false);
  const menuItems = useMemo<MenuItem[]>(
    () => filteredList.map<MenuItem>(value => ({
      value,
      icons: type === 'icon' ? [{prefix: "fas", iconName: value as IconName}] : undefined
    })),
    [filteredList, type]);

  const textColor = useMemo(() => {
    return !oriList || oriList.length === 0 || oriList.includes(input)
      ? 'white' : allowCustomInput ? 'lightskyblue' : 'orangered'
  }, [oriList, input, allowCustomInput]);

  const ref = useRef<HTMLInputElement>(null)
  const onBlurRef = useRef<((selectedItem?: (string | undefined)) => void) | null>(null)

  const showOptionsPopup = useCallback((edit: string) => {
    const found = !edit
      ? oriList
      : oriList.filter(item => item.toLowerCase().includes(edit.toLowerCase()));
    if (found.length === 0) return;
    setFilteredList(found);
    if (found.includes(edit) && found.length === 1) {
      setIndex(-1);
      return;
    }
    if (edit.length > 0)
      setShowPopup(true);
    setIndex(0);
  }, [oriList, setFilteredList, setShowPopup, setIndex])

  const inputChanged = useCallback((uncheckedInput: string) => {
    if (uncheckedInput === undefined) return false;
    const checkedInput = uncheckedInput.trim()
    if (input === checkedInput) return false;
    setInput(checkedInput);
    showOptionsPopup(checkedInput);
    onEditValue && onEditValue({key: name, value: checkedInput})
    return true;
  }, [name, onEditValue, input, setInput, showOptionsPopup])

  const selectItem = useCallback((choice: string) => {
    setShowPopup(false);
    if (!inputChanged(choice)) return;
    onBlurRef.current!(choice);
    setIndex(-1);
  }, [setShowPopup, inputChanged, setIndex, onBlurRef]);

  const onItemSelected = useCallback(({value: choice}) => {
    selectItem(choice);
    (type === 'string') && ref.current?.setSelectionRange(0, input.length);
  }, [input, selectItem, ref, type]);

  const openPopupAtInput = useCallback(() => {
    resetFilteredList();
    setIndex(oriList.indexOf(input));
    setShowPopup(!!oriList.length);
  }, [oriList, input, resetFilteredList, setIndex, setShowPopup]);

  const revert = useCallback(() => {
    setShowPopup(false);
    setIndex(-1);
    resetFilteredList();
    inputChanged(`${value}`);
  }, [setIndex, resetFilteredList, setShowPopup, inputChanged, value])

  const onKeyDown = useCallback(event => {
    const {code} = event;
    if (['Enter', 'Tab'].includes(code)) {
      if (index < 0 || !showPopup) return;
      selectItem(filteredList[index]);
    } else if (code === 'ArrowDown') {
      if (showPopup) {
        setIndex(Math.min(index + 1, filteredList.length - 1));
      } else {
        setIndex(0);
        setShowPopup(true);
      }
    } else if (code === 'ArrowUp') {
      if (!showPopup) return;
      setIndex(Math.max(index - 1, 0));
    } else if (code === 'Space') {
      if (showPopup) {
        index >= 0
          ? onItemSelected(filteredList[index])
          : setShowPopup(false);
      } else {
        openPopupAtInput()
      }
    } else if (code === 'Escape') {
      if (showPopup) {
        setShowPopup(false);
        setIndex(-1)
      } else
        revert()
    }
  }, [selectItem, openPopupAtInput, onItemSelected, revert, index, setIndex, showPopup, setShowPopup, filteredList]);

  const onInputChange = useCallback(edit => {
    if (edit === ' ') return;
    const trimmed = `${edit}`.trim();
    if (trimmed === input) return;
    inputChanged(edit);
  }, [inputChanged, input]);

  const onBlur = useCallback((selectedItem?: string) => {
    setShowPopup(false);
    if (_.isEqual(selectedItem || input, value)) return
    onChange && onChange({key: name, value: selectedItem || input});
  }, [input, onChange, name, setShowPopup, value]);

  const onInputFocus = useCallback((event?: { currentTarget: EventTarget & HTMLElement }) => {
    type === 'string' && ref.current?.setSelectionRange(0, input.length);
    onFocus && onFocus(event);
  }, [type, ref, input, onFocus]);

  // set the onBlur in the ref here. It can't be set before, because the onBlur
  // uses a prior hook itself.
  useEffect(() => {
    onBlurRef.current = onBlur;
  }, [onBlur, onBlurRef]);

  return (
    type ?
      <div
        onFocus={onFocus}
        onClick={onFocus}
        className='Property SelectList'>
        <span>{label || name}</span>
        <input
          ref={ref}
          value={input}
          type={type === 'number' ? "number" : "text"}
          style={{color: textColor}}
          onKeyDown={onKeyDown}
          onChange={event => onInputChange(event.currentTarget.value)}
          onBlur={() => onBlur()}
          onBlurCapture={event => {
            if (showPopup)
              event.stopPropagation();
          }}
          onDoubleClick={openPopupAtInput}
          onFocus={onInputFocus}
        />
        <FontAwesomeIcon icon={faAngleDown} className={'menu-btn'}/>
        {showPopup ? <PopupMenu
          align={"right"}
          items={menuItems}
          indexState={[index, setIndex]}
          onItemClick={onItemSelected}
          onClose={revert}
        /> : null}
      </div>
      : null
  );
}
