import { QuestionUnion } from "../questionnaires/entities/question";
import { createModel } from "@rematch/core";
import { Dispatch, RootModel, RootState } from "./index";
import {
  findUniqueChild,
  findUniqueChildAndContext,
  isNested,
  mutateUniqueChildOfQuestion,
  PropertyKeyValue,
  UniqueObject
} from "../questionnaires/entities/anyObject";
import { QuestionFactory } from "../questionnaires/factories/questionFactory";
import { PropertyBuilder, PropertyTemplateBuilder } from "../questionnaires/factories/propertyBuilder";
import { applyTemplate } from "./questionnaireFunctions";

export interface QuestionEditorState {
  after?: QuestionUnion,
  currentProperty?: keyof QuestionUnion,
  templateBuilder?: PropertyTemplateBuilder,
}

const initialState: QuestionEditorState = {
  after: undefined,
  currentProperty: undefined,
  templateBuilder: undefined,
}

export type QuestionProperty = UniqueObject & {
  key: keyof QuestionUnion,
  value: any,
}

export type AddItemPayload = {
  parent: { propKey: string } & UniqueObject,
  child: UniqueObject
}

export const questionEditorModel = createModel<RootModel>()({
  name: 'questionEditorModel',
  state: initialState,
  reducers: {
    set: (state, payload: QuestionEditorState) => ({
      ...state,
      ...payload
    }),
    reset: () => initialState,
    // item = one object in a array property of the question
    changeItemValues: (state, value: UniqueObject) => {
      if (!state.after) return state
      // immer lets us just mutate the state. it is a wrapper around a state object
      state.after = mutateUniqueChildOfQuestion(state.after, value);
    },
    deleteItem: (state, uuid: string) => {
      if (!state.after) return state
      let prop = findUniqueChildAndContext(state.after, uuid);
      if (!prop.parent) return state
      // immer lets us just mutate the state. it is a wrapper around a state object
      if (prop.indexes !== undefined)
        prop.parent.splice(prop.indexes[0], 1);
      else if (prop.key !== undefined)
        delete prop.parent[prop.key];
    },
    addItem: (state, {parent, child}: AddItemPayload) => {
      if (!state.after) return state
      let prop = findUniqueChild(state.after, parent.__uuid) as UniqueObject;
      if (!prop) return state
      // immer lets us just mutate the state. it is a wrapper around a state object
      if (isNested(prop[parent.propKey]))
        prop[parent.propKey].push([child]);
      else
        prop[parent.propKey].push(child);
    },
    setEditedQuestion: (state, q?: QuestionUnion) => ({
      ...state,
      after: q,
      currentProperty: undefined,
      templateBuilder: undefined,
    }),
    setCurrentProperty: (state, key: string | number | undefined) => ({
      ...state,
      currentProperty: key,
      templateBuilder: key ? PropertyBuilder.getPropertyTemplate(key) : undefined,
    }),
  },
  effects: (dispatch: Dispatch) => {
    // this destructuring is necessary for tsc. Else it will detect a circular reference and can't make up the models type
    const {questionEditorModel, questionnaireModel} = dispatch;
    return ({
      set: () => {questionnaireModel.applyEditedQuestion(undefined);},
      changeItemValues: () => {questionnaireModel.applyEditedQuestion(undefined);},
      deleteItem: () => {questionnaireModel.applyEditedQuestion(undefined);},
      addItem: () => {questionnaireModel.applyEditedQuestion(undefined);},

      // to do a value change that can have side effects on the object structure
      changeItemPropertyValue: (prop: PropertyKeyValue, state: RootState) => {
        const {after, templateBuilder} = state.questionEditorModel;
        if (!prop.__uuid || !after) return;
        let item = findUniqueChild(after, prop.__uuid) as UniqueObject;
        if (!item) return;
        item = {
          ...item,
          [prop.key]: prop.value,
        };
        const temp = templateBuilder && templateBuilder(prop);
        if (temp && temp.length > 0) {
          item = applyTemplate(item, temp)
        }
        questionEditorModel.changeItemValues(item);
      },

      // a direct property of the question, only the upper layer
      changePropertyValue: (property: PropertyKeyValue, state: RootState) => {
        const {key, value} = property as QuestionProperty;
        const {after} = state.questionEditorModel;
        if (!after) return;
        let prop = {...after, [key]: value};
        if (key === 'QuestionType') {
          prop = QuestionFactory.create(value, prop)
        }
        questionEditorModel.set({
          after: prop,
          currentProperty: key,
        });
      },
      // addProperty won't discriminate against a property that does does not belong to
      // the QuestionType
      addProperty: (type: keyof QuestionUnion, state: RootState) => {
        const {after} = state.questionEditorModel;
        const props = PropertyBuilder.propertyFactory(type);
        if (!after || after[type] || !props) return;
        questionEditorModel.set({
          after: {
            ...after,
            ...props
          },
          currentProperty: type,
        })
      },
      deleteProperty: (type: (keyof QuestionUnion) | undefined, state: RootState) => {
        const {after, currentProperty} = state.questionEditorModel;
        const key = type || currentProperty;
        if (!after || !key) return;
        const newAfter = {...after};
        delete newAfter[key];
        questionEditorModel.set({
          after: newAfter,
          currentProperty: undefined,
        });
      },
      refreshEdited: (_?: undefined, state?: RootState) => {
        const uuid = state?.questionEditorModel?.after?.__uuid;
        if (!uuid) return;
        const {setEditedQuestion} = dispatch.questionEditorModel;
        setEditedQuestion(
          state?.questionnaireModel?.present.questions.filter((q: QuestionUnion) => q.__uuid === uuid)[0]
        )
      },
    });
  }
})
