import './Property.scss';
import React, { useCallback, useEffect, useMemo, useState } from "react";
import { QuestionKey } from "../../../questionnaires/entities/question";
import { PropertyKeyValue } from "../../../questionnaires/entities/anyObject";
import { PropertySelectList } from "./PropertySelectList";
import { Path, PathMakerOptions } from "../../../questionnaires/entities/selector";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faLevelUpAlt } from '@fortawesome/free-solid-svg-icons'
import * as _ from "lodash";
import { usePropState } from "../../hooks/usePropState";

type PropertyPathMakerProps = {
  name: QuestionKey
  label?: string | JSX.Element,
  value: Path
  pathMakerOptions?: PathMakerOptions
  onChange?: (p: PropertyKeyValue) => void
  onFocus?: (event?: { currentTarget: EventTarget & HTMLElement }) => void
}

const isValid = (input: string | number): boolean =>
  input !== undefined &&
  (typeof input === 'number' ||
    (typeof input === 'string' && input.trim() !== "")
  )

function ensureOneItem(arr: (string | number)[]): (string | number)[] {
  if (arr.length === 0) return ['']
  return arr
}

export const PropertyPathMaker: React.FunctionComponent<PropertyPathMakerProps> = (
  {
    name,
    label,
    value,
    pathMakerOptions,
    onChange = undefined,
    onFocus,
  }) => {
  const [ori] = usePropState(ensureOneItem(value.filter(isValid)));
  const [path, setPath] = usePropState([...ori]);
  const [doSubmitWhenReady, setDoSubmitWhenReady] = usePropState(false);
  const [hasChanges, setHasChanges] = useState(false);
  const arrow = useMemo(() => <FontAwesomeIcon
      icon={faLevelUpAlt}
      style={{marginLeft: "30px", transform: "rotate(90deg)"}}/>
    , []);

  useEffect(() => {
    if (!hasChanges && !_.isEqual(ori, path.filter(isValid)))
      setHasChanges(true);
  }, [ori, path, setHasChanges, hasChanges]);

  const getOptionsOrType = useCallback(
    (path: Path, index: number) => pathMakerOptions && pathMakerOptions(name, path.slice(0, index)),
    [name, pathMakerOptions]);
  const getOptions = useCallback(
    (path: Path, index: number) => {
      const out = getOptionsOrType(path, index);
      if (typeof out === 'string') return;
      return out;
    },
    [getOptionsOrType]
  );
  const getType = useCallback(
    (path: Path, index: number): "string" | "number" | undefined => {
      const out = getOptionsOrType(path, index);
      if (!out) return undefined;
      if (!_.isArray(out)) {
        // @ts-ignore
        return ["number", "string"].includes(out) ? out : undefined;
      }
      return "string";
    },
    [getOptionsOrType]
  );

  const updatePath = useCallback((index?: number, edit?: string) => {
    index = index !== undefined ? index : path.length - 1;
    edit = edit !== undefined ? edit : `${path.slice(-1)[0]}`;
    setPath(path.slice(0, index).concat(edit.trim()));
  }, [path, setPath])

  const editedPath = useMemo((): Path => {
    return getType(path, path.length) ? path.concat('') : path;
  }, [getType, path]);

  const onSubmit = useCallback(() => {
    setDoSubmitWhenReady(true);
  }, [setDoSubmitWhenReady]);

  useEffect(() => {
    if (!hasChanges || !doSubmitWhenReady) return
    onChange && onChange({key: name, value: path.filter(v => v === 0 || v)});
    setHasChanges(false);
    setDoSubmitWhenReady(false);
  }, [doSubmitWhenReady, setDoSubmitWhenReady, path, name, onChange, hasChanges])

  return (
    <div
      onFocus={onFocus}
      onClick={onFocus}
      className='PropertyPathMaker'>
      {editedPath && editedPath.map((segment, index) =>
        <PropertySelectList
          key={index}
          name={`${name}`}
          label={index ? arrow : (label || name)}
          value={segment}
          options={getOptions(path, index)}
          type={getType(path, index)}
          onChange={() => onSubmit()}
          onEditValue={({value}) => updatePath(index, value)}
        />
      )}
    </div>
  );
}
