import React, { MouseEvent, useCallback, useEffect, useState } from "react";
import { Node } from 'react-flow-renderer'
import { QuestionContextMenu } from "./QuestionContextMenu";
import { Position, QuestionUnion } from "../../questionnaires/entities/question";
import { useDiagram } from "../hooks/useSelector";
import { useDispatchDiagram } from "../hooks/useDispatch";
import { PaneContextMenu } from "./PaneContextMenu";
import { NodeTypeNames } from "../Diagram/Types";

export function useDiagramContextMenu(setContextMenu: React.Dispatch<React.SetStateAction<JSX.Element | null>>) {
  const [nodes, setNodes] = useState<Node<QuestionUnion>[]>([]);
  const [pos, setPos] = useState<Position>({x: 100, y: 100});
  // for some reason the selected nodes give through the flow event onSelectionContextMenu
  // is not providing the correct selection sometimes, so we look them up ourselves.
  // careful: selectedElements returns a different type then (no Node or Edge) then typescript suggests
  const {selectedNodes} = useDiagram();
  const {showContextMenu, selectNodes} = useDispatchDiagram();

  const onClose = useCallback(() => {
    // console.log('close popup diagram')
    showContextMenu(false)
  }, [showContextMenu]);

  const onPaneMenu = useCallback((event: MouseEvent) => {
    event.preventDefault();
    setContextMenu(<PaneContextMenu
      key="ContextMenu"
      position={{x: event.clientX, y: event.clientY}}
      onClose={onClose}
    />)
    showContextMenu(true);
  }, [setContextMenu, onClose, showContextMenu])

  useEffect(() => {
    setContextMenu(<QuestionContextMenu
      key="ContextMenu"
      nodes={nodes}
      position={pos}
      onClose={onClose}
    />)
    showContextMenu(true);
  }, [nodes, pos, onClose, setContextMenu, showContextMenu]);

  const onSelectionMenu = useCallback((event: MouseEvent) => {
    event.preventDefault();
    setPos({x: event.clientX, y: event.clientY});
    // here we would like to usa the param from the handler, but it is not always
    // correct.
    // console.log('context selection', selectedNodes)
    setNodes(selectedNodes)
  }, [setPos, setNodes, selectedNodes]);

  const onNodeMenu = useCallback((event: MouseEvent, node: Node) => {
    event.preventDefault();
    if (node.type !== NodeTypeNames.Question) return;
    setPos({x: event.clientX, y: event.clientY});
    if (!selectedNodes.some(n => n.id === node.id)) {
      selectNodes([node.id])
      setNodes([node]);
    } else {
      setNodes(selectedNodes);
    }
  }, [setPos, setNodes, selectedNodes, selectNodes])

  return {
    onPaneMenu,
    onNodeMenu,
    onSelectionMenu,
  }
}