import { RootState } from "../../models";
import { useSelector } from "react-redux";
import { useMemo } from "react";
import { store } from "../../store/store";

export function useQuestionnaire() {
  return useSelector((s: RootState) => {
    return {
      ...s.questionnaireModel.present,
      ...s.questionnaireModel
    };
  })
}

const {selectedNodes: sn, selectedEdges: se} = store.select.diagramModel;

export function useDiagram() {
  const {questionNodes, hookNodes, questionEdges, hookEdges, ...rest} = useSelector((s: RootState) => s.diagramModel);
  const selectedNodes = useSelector((s: RootState) => sn(s));
  const selectedEdges = useSelector((s: RootState) => se(s));
  return {
    nodes: useMemo(() => [...hookNodes, ...questionNodes].map(x => ({...x})), [hookNodes, questionNodes]),
    edges: useMemo(() => [...hookEdges, ...questionEdges].map(x => ({...x})), [hookEdges, questionEdges]),
    questionNodes, questionEdges,
    hookNodes, hookEdges,
    selectedNodes, selectedEdges,
    ...rest
  }
}

export function useQuestionEditor() {
  return useSelector((s: RootState) => s.questionEditorModel)
}

export function useDocument() {
  return useSelector((s: RootState) => s.documentModel)
}
