import {
  InputTypes,
  QuestionType,
  QuestionTypes,
  QuestionUnion,
  WhenToShowType,
  WhenToShowTypes
} from "../entities/question";
import { createOption, createOptionsSelector, OptionsSelector, QuestionOption } from "../entities/options";
import {
  contentOptionsBuilder,
  createTriggeredBy,
  TriggeredBy,
  TriggeredByKey,
  TriggeredByTypes
} from "../entities/triggeredBy";
import { createTrigger, Trigger, TriggerKey, triggerTemplateBuilder, TriggerTypes } from "../entities/trigger";
import { createSideEffect, SideEffect, SideEffectTypes } from "../entities/sideEffect";
import { createRequestParams, RequestParam, RequestType, RequestTypes } from "../entities/request";
import { createSelector, PathMakerOptions, Selector, selectorOptionsBuilder } from "../entities/selector";
import {
  Comparators,
  Comparison,
  ComparisonKey,
  comparisonTemplateBuilder,
  createValueComparison,
  LogicComparators,
  ValueComparators
} from "../entities/comparison";
import { Dictionary, KeyValue, UniqueObject } from "../entities/anyObject";
import { fas } from "@fortawesome/free-solid-svg-icons";
import { library } from "@fortawesome/fontawesome-svg-core";

const base = ["Title", "TriggeredBy", "Triggers", "SideEffect", "WhenToShow", "DontSend"];
export type PropertyName = keyof QuestionUnion;
export type QuestionProperty =
  UniqueObject[]
  | [[]]
  | [OptionsSelector]
  | SideEffect
  | RequestType
  | WhenToShowType
  | Selector
  | string
  | string[]
  | number
  | boolean;
export type PropertyItem = (QuestionOption | TriggeredBy | Trigger | SideEffect | OptionsSelector |
  RequestParam | WhenToShowType | Comparison);

export type PropertyItemNames = keyof PropertyItem;
export type PropertyElement = TriggerKey | TriggeredByKey | ComparisonKey;

export type PropertyValueOptionsBuilder =
  string[]
  | "undefined"
  | "object"
  | "boolean"
  | "number"
  | "string"
  | "function"
  | "symbol"
  | "bigint"
  | undefined;

export type PropertyTemplateBuilder = (prop: KeyValue<any>) => KeyValue<any>[] | undefined;

// 'values' means a string, number or array with either strings or numbers
export type PropertyValueOptions = PathMakerOptions | string[] | undefined | 'string' | 'number' | 'json' | 'values';
export type GetPropertyValueOptionsType = (propName: string, itemName?: string) => PropertyValueOptions;

library.add(fas);
// @ts-ignore
export const faIcons = Object.keys(library['definitions'].fas)

export interface UiBuilder {
  giveQuestionPropertyNames(type: QuestionType): string[];

  propertyFactory(prop: PropertyName): { [key: PropertyName]: QuestionProperty };

  propertyItemFactory(prop: PropertyName): ((type?: string) => UniqueObject) | undefined;

  getPropertyTemplate(propName: PropertyName): PropertyTemplateBuilder | undefined;

  getPropertyValueOptions: GetPropertyValueOptionsType;

  getPropertyItemOrder(propName: string): string[];

  setPropertyFactory(propName: string, factory: () => { [key: PropertyName]: QuestionProperty }): void,

  setPropertyItemFactory(propName: string, factory: ((type?: string) => UniqueObject) | undefined): void,

  setGetPropertyTemplate(propName: string, optionsBuilder: PropertyTemplateBuilder | undefined): void,

  setGetPropertyValueOptions(propName: string, optionsBuilder: PropertyValueOptions): void,
}

type BuilderPlugins = {
  propertyFactory: Dictionary<() => { [key: PropertyName]: QuestionProperty }>,
  propertyItemFactory: Dictionary<(() => UniqueObject) | undefined>,
  getPropertyTemplate: Dictionary<PropertyTemplateBuilder | undefined>,
  getPropertyValueOptions: Dictionary<PropertyValueOptions>,
};

export class PropertyBuilder {
  private static _plugins: BuilderPlugins = {
    propertyFactory: {},
    propertyItemFactory: {},
    getPropertyTemplate: {},
    getPropertyValueOptions: {},
  };

  static setPropertyFactory(propName: string, factory: () => { [key: PropertyName]: QuestionProperty }) {
    PropertyBuilder._plugins.propertyFactory[propName] = factory;
  }

  static setPropertyItemFactory(propName: string, factory: (() => UniqueObject) | undefined) {
    PropertyBuilder._plugins.propertyItemFactory[propName] = factory;
  }

  static setGetPropertyTemplate(propName: string, builder: PropertyTemplateBuilder | undefined) {
    PropertyBuilder._plugins.getPropertyTemplate[propName] = builder;
  }

  static setGetPropertyValueOptions(propName: string, builder: PropertyValueOptions) {
    PropertyBuilder._plugins.getPropertyValueOptions[propName] = builder;
  }

  static giveQuestionPropertyNames(type: QuestionType): string[] {
    switch (type) {
      case "Alert":
        return base.concat(['Message', 'MessageSelector']);
      case "Text":
        return base.concat(['InputType']);
      case "Select":
        return base.concat(['Options', 'OptionsSelector']);
      case "AutoAnswer":
        return base.concat(['Comparisons', 'AnswerSelector', 'AnswerValue']);
      case "FunctionCall":
        return base.concat(['FunctionSelector', 'FunctionArguments']);
      case "RoadlinkRequest":
        return base.concat(['RequestType', 'RequestParams']);
      default :
        return base;
    }
  }

  static propertyFactory(propName: PropertyName): { [key: PropertyName]: QuestionProperty } {
    const o = PropertyBuilder._plugins.propertyFactory[propName];
    if (o) return o();

    switch (propName) {
      case "TriggeredBy":
        return {[propName]: [], SideEffect: [createSideEffect('Block')]};
      case "Triggers":
        return {[propName]: []};
      case "SideEffect":
        return {[propName]: [createSideEffect('Block')]};
      case "Options":
        return {[propName]: []};
      case "OptionsSelector":
        return {[propName]: [createOptionsSelector()]};
      case "AnswerSelector":
        return {[propName]: createSelector()};
      case "AnswerValue":
        return {[propName]: 0};
      case "Comparisons":
        return {[propName]: []};
      case "FunctionSelector":
        return {[propName]: []};
      case "FunctionArguments":
        return {[propName]: []};
      case "MessageSelector":
        return {[propName]: createSelector()};
      case "WhenToShow":
        return {[propName]: "now" as WhenToShowType};
      case "RequestType":
        return {[propName]: "RequestCheckBarcodeExist" as RequestType};
      case "RequestParams":
        return {[propName]: []};
      case "DontSend":
        return {[propName]: true};
      default:
        return {[propName]: ''};
    }
  }

  static propertyItemFactory(propName: PropertyName): ((type?: string) => UniqueObject | [UniqueObject]) | undefined {
    const o = PropertyBuilder._plugins.propertyItemFactory[propName];
    if (o) return o;

    switch (propName) {
      case "TriggeredBy":
        return () => createTriggeredBy();
      case "Triggers":
        return createTrigger;
      case "Options":
        return createOption;
      case "OptionsSelector":
        return createOptionsSelector;
      case "Comparisons":
        return createValueComparison;
      case "SideEffect":
        return createSideEffect;
      case "RequestParams":
        return createRequestParams
      default:
        return undefined;
    }
  }

  static getPropertyTemplate(propName: PropertyName): PropertyTemplateBuilder | undefined {
    const o = PropertyBuilder._plugins.getPropertyTemplate[propName];
    if (o) return o;
    switch (propName) {
      case "Triggers":
        return triggerTemplateBuilder;
      case "Comparisons":
        return comparisonTemplateBuilder;
    }
  }

  static getPropertyItemOrder(propName: string): string[] {
    switch (propName) {
      case "Triggers":
        return ['TriggerType', 'Value', 'QuestionKeys']
      case "Comparisons":
        return ['LeftHand', 'LeftHandValue', 'LeftHandSelector', 'LeftHandComparison', 'Comparator', 'RightHand', 'RightHandValue', 'RightHandSelector', 'RightHandComparison']
      default:
        return [];
    }
  }

  static getPropertyValueOptions: GetPropertyValueOptionsType = (propName: string): PropertyValueOptions => {
    const o = PropertyBuilder._plugins.getPropertyValueOptions[propName];
    if (o) return o;

    switch (propName as PropertyItemNames) {
      // properties
      case "SideEffectType":
        return [...SideEffectTypes];
      case "WhenToShow":
        return [...WhenToShowTypes];
      case "RequestType":
        return [...RequestTypes];
      // case "RequestParams":
      // maybe this should be a sub category of RequestParams somehow. Needs a lot of refactor to make that possible
      case "GoodId":
      case "TraceTypeId":
        return selectorOptionsBuilder;
      case "QuestionType":
        return [...QuestionTypes];
      case "QuestionKeys":
        return () => []
      case "InputType":
        return [...InputTypes];
      case "RightHandValue" :
      case "LeftHandValue" :
      case "Value" :
        return 'values'
      case "OptionsSelector" :
        return selectorOptionsBuilder;
      case "AnswerSelector":
        return selectorOptionsBuilder;
      case "MessageSelector":
        return selectorOptionsBuilder;
      case "Selector":
        return selectorOptionsBuilder;
      case "Comparator":
        return [...Comparators];
      case "ValueComparator": // a virtual property, json won't contain this
        return [...ValueComparators];
      case "LogicComparator": // a virtual property, json won't contain this
        return [...LogicComparators];
      case "LeftHand":
      case "RightHand":
        return ["Selector", "Value", "Comparison"];
      case "LeftHandSelector":
      case "RightHandSelector":
        return selectorOptionsBuilder;
      case "LeftHandComparison":
      case "RightHandComparison":
        return undefined;
      case "TriggerType":
        return [...TriggerTypes];
      case "TriggeredByType":
      case "Type":
        return [...TriggeredByTypes];
      case "Content":
        return contentOptionsBuilder;
      case "FunctionSelector":
        return () => [];
      case "FunctionArguments":
        return 'json'; // will give multiline edit box with json validation
      case "Key" :
      case "Title" :
      case "Message" :
        return 'string'
      case "Icon" :
        return faIcons
      case "AnswerValue" :
        return undefined
    }
  }
}
