import { Selector } from "./selector";
import { createUniqueObject, UniqueObject } from "./anyObject";
import * as _ from "lodash";

export const RequestTypes = ["RequestActivities", "RequestCheckBarcodeExist", "RequestColorProfile",
  "RequestDialNumbers", "RequestLogo", "RequestOperators", "RequestPackingList", "RequestProblemstatusList",
  "RequestSettingsOperator", "RequestTaskTypeExList", "RequestTraceTypes", "RequestVehicles",
  "RequestQuestionnairesProfile"
] as const;
export type RequestType = typeof RequestTypes[number];
export type RequestParam = UniqueObject & {
  [key: string]: Selector | string | number | string[],
};

const objects = {
  "RequestActivities": {},
  "RequestCheckBarcodeExist": {
    "Barcode": ""
  },
  // "RequestColorProfile": {},
  "RequestDialNumbers": {},
  "RequestLogo": {"LogoId": 1},
  "RequestOperators": {},
  "RequestPackingList": {},
  "RequestProblemstatusList": {},
  "RequestSettingsOperator": {},
  "RequestTaskTypeExList": {},
  "RequestTraceTypes": {},
  "RequestVehicles": {},
  "RequestQuestionnairesProfile": {
    "GoodId": 1,
    "TraceTypeId": 1
  },
}
type RequestParamsType = typeof objects

export const createRequestParams = (type?: string): UniqueObject =>
  createUniqueObject(_.cloneDeep(objects[type as keyof RequestParamsType]));
