import React from "react";
import './ToolBar.scss';
import { useEditingItems } from "../hooks/useEditingItems";
import { Map } from "./Map";
import { MenuItem } from "./PopupMenu";
import { Btn } from "./Btn";

const onItemClick = (item: MenuItem) => {
  if (typeof item.value === 'function') item.value();
  else console.warn('Menu item value is not a function:', item.value)
}

const ToolItemRenderer = (menuItem: MenuItem, i: number) => {
  const {value, icons, disabled, toolTip, hotkeys, title} = menuItem;
  const isSeparator = value === 'separator';
  return (
    <div className={isSeparator ? "Separator" : "MenuItem"} key={i} id={title}>
      {isSeparator ? null :
        <Btn
          disabled={disabled}
          icon={icons}
          onClick={() => onItemClick(menuItem)}
          toolTip={[toolTip, hotkeys].filter(v => v).join(' : ')}
        />
      }
    </div>
  )
}

export const ToolBar = function () {
  const {fullEditMenuItems: menuItems} = useEditingItems();

  return <Map className="ToolBar" items={menuItems} itemRenderer={ToolItemRenderer}/>
}