import React, { useCallback, useEffect } from "react";
import { MenuAlign, MenuItem, PopupMenu } from "./PopupMenu";
import { If } from "./If";
import { DropDownBtn } from "./DropDownBtn";
import { BtnProps } from "./Btn";
import hotkeys from 'hotkeys-js';
import { getTargetClass } from "../../core/GetAttribute";

export interface DropDownMenuProps extends BtnProps {
  menuTitle?: string;
  menuItems: MenuItem[];
  condition?: boolean;
  align?: MenuAlign;
}

export function DropDownMenu({menuItems, menuTitle, condition, align, ...props}: DropDownMenuProps) {
  const onItemClick = useCallback((item: MenuItem) => {
    if (typeof item.value === 'function' && !item.disabled) item.value();
    else console.warn('Menu item value is not a function:', item.value)
  }, []);

  useEffect(() => {
    menuItems.filter(({hotkeys: h}) => h)
      .forEach(({hotkeys: key, ...item}) => {
        hotkeys(key!, (event) => {
          if (getTargetClass(event)?.includes('react-flow')) {
            event.preventDefault();
            item.value('pointer')
          }
        })
      })

    return () => {
      menuItems.filter(({hotkeys: h}) => h)
        .forEach(({hotkeys: key, ...item}) => {
          hotkeys.unbind(key!)
        });
    }
  }, [onItemClick, menuItems]);

  const menu = useCallback(({onClose}) => <PopupMenu
      menuTitle={menuTitle}
      onClose={onClose}
      items={menuItems}
      onItemClick={onItemClick}
      align={align}
    />
    , [menuItems, onItemClick, menuTitle, align]);

  return <If condition={condition === undefined || condition}>
    <DropDownBtn menu={menu} {...props}/>
  </If>
}