export const QuestionBaseTemplates = QuestionTypes.reduce<{ [key: string]: QuestionTemplateFactory }>((f, t) => {
  f[t] = (template?: Optional<QuestionUnion> | undefined) => QuestionFactory.create(t, template)
  return f;
}, {})

export class QuestionFactory {
  // careful, the template does not type check or runtime check if the properties belong to
  // the created question type. This must be done by caller.
  public static create(
    type: QuestionType,
    template?: Optional<QuestionUnion> | undefined
  ): QuestionUnion {
    // @ts-ignore
    if (!QuestionFactory[type]) {
      return createQuestionBase(type);
    }
    let keep: AnyObject = {};
    if (template) {
      keep = _.cloneDeep(template) as AnyObject;
      Object.keys(keep).forEach(
        k => {
          if (!['SideEffect', 'Title', 'TriggeredBy', 'Triggers', 'WhenToShow',
            '__position', '__workflowId'].includes(k))
            delete keep[k]
        }
      )
    }
    // @ts-ignore
    const q = {
      // @ts-ignore
      ...QuestionFactory[type](),
      ...keep
    }
    return q
  }

  public static Text(): QuestionText {
    return {
      ...createQuestionBase('Text'),
      InputType: "text",
    }
  }

  public static Alert(): QuestionAlert {
    return {
      ...createQuestionBase('Alert'),
      Message: "Dan weet je dat.",
    }
  }

  public static Select(): QuestionSelect {
    return {
      ...createQuestionBase('Select'),
      Options: [
        createUniqueObject({Title: "keuze 1", Value: 0}),
        createUniqueObject({Title: "keuze 2", Value: 1}),
      ],
      Triggers: [
        {__uuid: v4(), TriggerType: "Equals", Value: 0, QuestionKeys: []},
        {__uuid: v4(), TriggerType: "Equals", Value: 1, QuestionKeys: []},
      ]
    }
  }

  public static AutoAnswer(): QuestionAutoAnswer {
    return {
      ...createQuestionBase('AutoAnswer'),
      Comparisons: [createLogicComparison()],
      Triggers: [
        {__uuid: v4(), TriggerType: "Equals", Value: [false], QuestionKeys: []},
        {__uuid: v4(), TriggerType: "Equals", Value: [true], QuestionKeys: []},
      ]
    }
  }
}