import { QuestionKey, QuestionUnion } from "./question";
import { createUniqueObject, UniqueObject } from "./anyObject";
import { ComparisonValue, createValueComparison } from "./comparison";
import { PropertyTemplateBuilder } from "../factories/propertyBuilder";

export const TriggerTypes = ["Any", "All", "Equals", "Regex", "GT", "LT", "Always", "Comparison", "PersistentButton"] as const;
export type TriggerType = typeof TriggerTypes[number];

export type Trigger = UniqueObject & {
  TriggerType?: TriggerType,
  Value?: any,
  Questions?: QuestionUnion[],
  QuestionKeys?: QuestionKey[],
}

export type TriggerKey = keyof Trigger;

function createValueForType(type: TriggerType): number[] | string[] | string | number | undefined | ComparisonValue {
  if(["Always", "PersistentButton"].includes(type))
    return undefined;
  if(["Comparison"].includes(type))
    return createValueComparison()
  if(["All", "Any"].includes(type))
    return [1]
  if(["Regex"].includes(type))
    return "//g"
  return 0
}

export const createTrigger = (type?: string): Trigger => {
  const TriggerType = type as TriggerType || "Equals";
  const Value = createValueForType(TriggerType)

  return createUniqueObject({
    TriggerType,
    Value,
    QuestionKeys: [],
  });
}

//ObjectProperty template like builder: must be able to influence , add, delete properties in own object
// or give user selection of properties to use
// instead of return string[]  I return objects to KeyValue[] to indicate property name and value options for that new property
export const triggerTemplateBuilder: PropertyTemplateBuilder = ({key,value}) => {
  if (key === 'TriggerType') {
    return [{ key: 'Value', value: createValueForType(value as TriggerType)}]
  }
}
