import './QuestionItem.scss';
import "./GenericObjectComponent.scss";
import React, { useMemo, useState } from "react";
import { GenericObjectListComponent } from "./GenericObjectListComponent";
import GivePropertyComponent from "./PropertyEditor";
import { RequestProps, useOrderedProperties, useQuestionHandlers, useQuestionState } from "./QuestionItemController";
import { UniqueObject } from "../../questionnaires/entities/anyObject";
import { useBuilder } from "../hooks/useBuilder";
import { RequestComponent } from "./RequestComponent";
import { PropertyEditMenu } from "../Menus/PropertyEditMenu";
import { Label } from "../core/Label";
import * as _ from "lodash";
import { Row } from "../core/Layout";
import { useDispatchQuestionEditor, useDispatchQuestionnaire } from "../hooks/useDispatch";
import { Btn } from "../core/Btn";
import { faPlus } from "@fortawesome/free-solid-svg-icons";
import { color } from "../core/Colors";
import { KeyEditComponent } from "./PropertyEditor/KeyEditComponent";
import { faTrashAlt } from "@fortawesome/free-regular-svg-icons";
import { Property } from "./PropertyEditor/Property";
import { QuestionUnion } from "../../questionnaires/entities/question";

export const QuestionItem = () => {
  const [jsonEditMode, setJsonEditMode] = useState(false);
  const properties = useOrderedProperties();
  const {currentProperty, item, QuestionType} = useQuestionState()
  const {updateQuestion} = useDispatchQuestionnaire();
  const {
    onKeyPropChange,
    onChangeValue,
    onPropertyClick
  } = useQuestionHandlers()
  const builder = useBuilder();
  const {deleteProperty} = useDispatchQuestionEditor();
  const deletableProps = useMemo(() => QuestionType ? builder.giveQuestionPropertyNames(QuestionType) : [], [QuestionType, builder]);
  const canDelete = (prop: string) => deletableProps.includes(prop);
  const styleColor = useMemo(() => (QuestionType && {'--question-type-color': color(QuestionType)} as React.CSSProperties), [QuestionType]);

  if (!properties.length || !item) return <div>Nothing to show</div>;

  return (
    <div
      className={`QuestionItem ${QuestionType}`}
      style={styleColor}
    >
      <div className='key'>
        <Label text={`${item.QuestionType}`}/>
        <Label text={`${item.Key}`}/>
        <Btn label="{/}" className="json-edit" onClick={() => setJsonEditMode(!jsonEditMode)}/>
        <PropertyEditMenu/>
      </div>
      <div className='GenericObjectComponent'>
        {jsonEditMode
          ? <Property name="" value={item} changeOnBlurOnly={true}
                      onChange={({value}) => updateQuestion(value as QuestionUnion)}/>
          : properties.map(({key, value}) => {
            if (key) {
              if (builder.propertyItemFactory(key)) {
                if (!_.isArray(value)) {
                  console.log('strange value', key, value)
                  return <span key={key} style={{color: "red"}}>{JSON.stringify(value)}</span>
                }
                return <GenericObjectListComponent
                  key={key}
                  parent={item}
                  propName={key}
                  itemsNest={value as UniqueObject[] | UniqueObject[][]}
                  highlight={key === currentProperty}
                  onClick={() => onPropertyClick(key)}
                />;
              } else if (key === 'Request') {
                return <RequestComponent {...value as RequestProps} />
              } else
                return <Row
                  key={key}
                  className={`wrapper ${key === currentProperty ? 'highlight' : ''}`}>
                  {key === 'Key' ? <KeyEditComponent
                      name={key}
                      value={value}
                      onBlur={onKeyPropChange}
                      onFocus={() => onPropertyClick(key)}
                    /> :
                    <GivePropertyComponent
                      name={key}
                      value={value}
                      options={builder.getPropertyValueOptions(key)}
                      onChange={onChangeValue}
                      onFocus={() => onPropertyClick(key)}
                    />}
                  {canDelete(key)
                    ? <Btn icon={faTrashAlt} onClick={() => deleteProperty(key)} style={{height: '25px'}}/>
                    : <span style={{width: '33px'}}/>
                  }
                </Row>
            }
            return <div key={key} style={{color: "red"}}>SHOULD BE {key}: {JSON.stringify(value)}</div>;
          })}
      </div>
      <Row style={{justifyContent: 'flex-end'}}>
        <PropertyEditMenu label="More Properties" icon={faPlus}/>
      </Row>
    </div>
  )
}
