import { QuestionUnion } from "../questionnaires/entities/question";
import { AnyObject, createUniqueObject, KeyValue, UniqueObject } from "../questionnaires/entities/anyObject";
import { v4 } from "uuid";
import { ValueComparators } from "../questionnaires/entities/comparison";
import _, { isEmpty } from "lodash";
import { Connection } from "./questionnaireModel";
import { SideEffect } from "../questionnaires/entities/sideEffect";
import { TriggeredBy } from "../questionnaires/entities/triggeredBy";

export function isDoubleKey(questions: QuestionUnion[], key: string): boolean {
  return questions.filter(q => q.Key === key).length > 1
}

export function isSecondOccurrence(questions: QuestionUnion[], question: QuestionUnion): boolean {
  const doubles = questions.filter((q, i, arr) => q.Key === question.Key);
  return doubles.length > 1 && doubles[1] === question
}

export function getQuestionByKey(questions: QuestionUnion[], key: string): QuestionUnion | undefined {
  return questions.find(q => q.Key === key);
}

export function isExistingKey(questions: QuestionUnion[], key: string): boolean {
  return questions.some(q => q.Key === key);
}

export function giveUniqueNextKey(questions: QuestionUnion[], key: string): string {
  if (!isExistingKey(questions, key))
    return key;
  let keyPieces = key.split('_');
  const nr = parseInt(keyPieces.slice(-1)[0], 10);
  keyPieces = isNaN(nr)
    ? [...keyPieces, '1']
    : [...keyPieces.slice(0, -1), `${nr + 1}`]
  return giveUniqueNextKey(questions, keyPieces.join('_'));
}

export function giveUniqueKeys(questions: QuestionUnion[]): QuestionUnion[] {
  return questions.reduce<QuestionUnion[]>((newList, q) => {
    if (isSecondOccurrence(newList, q))
      q.Key = giveUniqueNextKey(newList, q.Key);
    return newList;
  }, questions)
}

export function changeKeyReferences(questions: QuestionUnion[], before: string, after: string): QuestionUnion[] {
  return questions.map(q => {
    if (q.Key === before) {
      return {...q, Key: after}
    }
    if (!q.Triggers) return q;
    return {
      ...q,
      Triggers: q.Triggers.map(t => ({
        ...t,
        QuestionKeys: t.QuestionKeys && t.QuestionKeys.map(qk => qk === before ? after : qk)
      }))
    }
  })
}

export function giveAllObjectsUuid(o: AnyObject | AnyObject[], override?: boolean) {
  if (_.isArray(o)) {
    o.forEach(c => giveAllObjectsUuid(c, override))
  } else if (o && typeof o === 'object') {
    try {
      o.__uuid = override ? v4() : (o.__uuid || v4());
      Object.keys(o).forEach(k => {
        const v = o[k];
        giveAllObjectsUuid(v, override);
      })
    } catch (e) {
      console.error('giveAllObjectsUuid error: ', e, o)
    }
  }
}

export function flattenNestedQuestions(questions: QuestionUnion[]): QuestionUnion[] {
  const flatLength = questions.length;
  const flatter = questions.reduce<QuestionUnion[]>((flattened, q) => {
    flattened.push(q);
    if (q.Triggers) {
      q.Triggers.map(tr => {
        if (tr.Questions) {
          const addedQuestions = tr.Questions.map<QuestionUnion>(nq => ({
            ...nq,
            Key: giveUniqueNextKey([...flattened, ...questions], nq.Key),
          }));
          const qKeys = addedQuestions.map(nq => nq.Key);
          flattened.push(...addedQuestions);
          tr.QuestionKeys = qKeys;
          delete tr.Questions
        }
        return tr;
      })
    }
    return flattened
  }, []);

  if (flatter.length > flatLength)
    return flattenNestedQuestions(flatter);

  return flatter
}

export function convertIncompatibleQuestion(q: QuestionUnion): QuestionUnion {
  if (q.QuestionType === 'AutoAnswer' && q.Comparisons && q.Comparisons.length > 0
    && (ValueComparators as readonly string[]).includes(q.Comparisons[0].Comparator)
  ) {
    return {
      ...q,
      Comparisons: [{
        __uuid: v4(),
        Comparator: "AND",
        Comparisons: q.Comparisons
      }]
    }
  }
  return q;
}

export function fixBrokenTriggerKeys(q: QuestionUnion): QuestionUnion {
  if (!q.Triggers) return q;
  return {
    ...q,
    Triggers: q.Triggers.map(tr => ({
      ...tr,
      QuestionKeys: tr.QuestionKeys?.filter(k => k.trim()).map(k => k.trim())
    }))
  }
}

export const ensureHasBlock = (ss: SideEffect[] = []): SideEffect[] =>
  ss.some(s => s.SideEffectType === 'Block')
    ? ss
    : ss.concat(createUniqueObject<SideEffect>({SideEffectType: "Block"}))

export function fixNonBlockingQuestionPath(q: QuestionUnion): QuestionUnion {
  if (!q.TriggeredBy || q.TriggeredBy.length === 0) return q;
  return {
    ...q,
    SideEffect: ensureHasBlock(q.SideEffect)
  }
}

export function applyTemplate(obj: UniqueObject, template: KeyValue<any>[]): UniqueObject {
  template.forEach(({key, value}) => {
    if (value !== undefined) {
      if (obj[key] === undefined || typeof obj[key] !== typeof value) {
        obj[key] = value;
      }
    } else {
      delete obj[key];
    }
  });
  return obj;
}

export function removeQuestionsWithKeys(questionsKeys: string[], questions: QuestionUnion[]): QuestionUnion[] {
  return questions.filter(q => !questionsKeys.includes(q.Key))
    .map(q => ({
      ...q,
      Triggers: q.Triggers && q.Triggers.map(tr => ({
        ...tr,
        QuestionKeys: tr.QuestionKeys?.filter(k => !questionsKeys.includes(k)),
      }))
    }));
}

export const getHookedQuestions = (questions: QuestionUnion[]) => questions.filter(q => !isEmpty(q.TriggeredBy) && !isEmpty(q.TriggeredBy![0]));
export const getQuestionHooks = (q: QuestionUnion) => q.TriggeredBy ? q.TriggeredBy.map<TriggeredBy>(tbs => tbs[0]) : undefined
export const getHooksWithQuestion = (questions: QuestionUnion[]) => getHookedQuestions(questions).map(q => ({
  h: getQuestionHooks(q) || [],
  q
}));

export const getQuestionWithHook = (questions: QuestionUnion[], hookId: string) => getHooksWithQuestion(questions)
  .find(({q, h}) => h?.length && h.find(h => h.__uuid === hookId))?.q;

export function getTreeFrom(q: QuestionUnion, questions: QuestionUnion[]): QuestionUnion[] {
  const keys = q.Triggers?.map(tr => tr.QuestionKeys || []).flat() || [];
  const qs = questions.filter(q => keys.includes(q.Key));
  return _.uniq([q, ...qs, ...qs.map(qi => getTreeFrom(qi, questions)).flat()])
}

export const createConnections = (questions: QuestionUnion[]) =>
  questions.filter(q => q.Triggers && q.Triggers.length)
    .reduce<Connection[]>((acc, q) => {
      return acc.concat(q.Triggers!.reduce<Connection[]>((acc2, tr) => {
        return acc2.concat(tr.QuestionKeys?.filter(_.trim)
          .map<Connection>(key => ({
            sourceKey: q.Key,
            targetKey: key,
            triggerId: tr.__uuid,
            id: tr.__uuid + key
          })) || []);
      }, []))
    }, [])