import React, { PropsWithChildren, useCallback, useMemo } from "react";
import "./RequestComponent.scss";
import { PropertyKeyValue } from "../../questionnaires/entities/anyObject";
import GivePropertyComponent from "./PropertyEditor";
import { useBuilder } from "../hooks/useBuilder";
import { RequestParam, RequestType } from "../../questionnaires/entities/request";
import { useDispatchQuestionEditor } from "../hooks/useDispatch";
import { useObjectTransformer } from "./GenericObjectComponentController";
import { PropertyFlexiblePathMaker } from "./PropertyEditor/PropertyFlexiblePathMaker";
import { PathMakerOptions } from "../../questionnaires/entities/selector";

export type RequestComponentProps = {
  requestType: RequestType,
  requestParams: [RequestParam]
}

export function RequestComponent(
  {
    requestType,
    requestParams,
  }: PropsWithChildren<RequestComponentProps>
) {
  const {changePropertyValue, changeItemValues} = useDispatchQuestionEditor();
  const builder = useBuilder();
  const typeOptions = useMemo(() => builder.getPropertyValueOptions('RequestType'), [builder]);
  const createParams = useMemo(() => builder.propertyItemFactory("RequestParams")!, [builder]);
  const params = useMemo(() => requestParams[0], [requestParams]);
  const {propertyList, onChange: onPropChange, getOptions} = useObjectTransformer({
    item: params,
    propName: 'RequestParams'
  });

  const onChange = useCallback((prop: PropertyKeyValue) => {
    const {key, value} = prop;
    if (key === 'RequestType' && value !== requestType) {
      changeItemValues({
        ...createParams(value as RequestType),
        __uuid: params.__uuid,
      })
    }
    changePropertyValue(prop);
  }, [changePropertyValue, changeItemValues, createParams, requestType, params.__uuid]);

  return (
    <div className={`RequestComponent`}>
      <div className="header">
        <span>Request</span>
      </div>
      <div className="ItemContainer">
        <GivePropertyComponent
          name="RequestType"
          value={requestType}
          options={typeOptions}
          onChange={onChange}
        />
        <div>
          <div className="header">
            <span>RequestParams</span>
          </div>
          <div
            className={`GenericObjectComponent`}
          >
            {propertyList.map(({key, value}, i) => {
              return <PropertyFlexiblePathMaker
                key={`${i}`}
                name={key}
                value={value}
                onChange={onPropChange}
                pathMakerOptions={getOptions(key) as PathMakerOptions}
              />
            })}
          </div>
        </div>
      </div>
    </div>
  );
}
