import './Property.scss';
import React, { useMemo } from "react";
import { Property } from "./Property";
import { PropertySelectList } from "./PropertySelectList";
import { PropertyKeyValue } from "../../../questionnaires/entities/anyObject";
import { PropertyPathMaker } from "./PropertyPathMaker";
import { PropertyObject } from "../PropertyObject";
import * as _ from "lodash";
import { PropertyValueOptions } from "../../../questionnaires/factories/propertyBuilder";

type PropertyEditorProps = {
  name: string
  value: any
  label?: string | JSX.Element,
  options?: PropertyValueOptions,
  onFocus?: () => void,
  onChange?: (p: PropertyKeyValue) => void
}

const GivePropertyComponent: React.FunctionComponent<PropertyEditorProps> = (
  {
    options,
    ...props
  }) => {
  const {name} = props;

  const component = useMemo(() => {
    if (!props || props.value === undefined) return null;
    if (_.isArray(options)) {
      if (options.length && typeof options[0] === 'string') {
        return <PropertySelectList options={options as string[]} {...props}
                                   type={name === 'Icon' ? 'icon' : 'string'}/>;
      }
    }
    if (typeof options === 'function') {
      return <PropertyPathMaker pathMakerOptions={options} {...props}/>;
    }
    if (props.value.__uuid)
      return <PropertyObject propName={name} item={props.value}/>

    return <Property {...props} type={options as ('string' | 'number' | undefined)}/>
  }, [options, props, name]);

  return component
}

export default GivePropertyComponent;